import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ConductorService } from './conductor.service';
import { TABLA_TIPO_SEXO, TABLA_TIPO_EMPRESA, TABLA_TIPO_CARGO, TABLA_TIPO_AREA, TABLA_TIPO_REGIMEN_PENSIONARIO, TABLA_TIPO_COMISION, TABLA_TIPO_BANCO, TABLA_TIPO_CATEGORIA_BREVETE, REGEXP_TELEFONO, REGEXP_DNI, REGEXP_CORREO, REGEXP_CUENTA, REGEXP_NOT_EMPTY } from './../constantes';
import { MiscelaneoService } from './../miscelaneo/miscelaneo.service';
import { SelectItem, Message } from 'primeng/primeng';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { OnInit, Component } from '@angular/core';
import { BaseComponent } from './../../comun/basecomponent';
import { Conductor } from './conductor';

@Component({
    selector: 'conductor-mantenimiento',
    templateUrl: './conductor-mantenimiento.component.html'
})
export class ConductorMantenimientoComponent extends BaseComponent implements OnInit {

    accionNombre: string = "";
    verModal: boolean = false;

    lstSexo: SelectItem[] = [];
    lstEmpresa: SelectItem[] = [];
    lstCargo: SelectItem[] = [];
    lstArea: SelectItem[] = [];
    lstRegPen: SelectItem[] = [];
    lstCom: SelectItem[] = [];
    lstBanco: SelectItem[] = [];
    lstCategoria: SelectItem[] = [];
    lstRoles: SelectItem[] = [];

    conductorForm: FormGroup;

    conductor: Conductor = new Conductor();

    constructor(private miscelaneoService: MiscelaneoService, private seguridadService: SeguridadService,
        private conductorService: ConductorService, private translateService: TranslateService, private fb: FormBuilder) {

        super();
    }

    ngOnInit() {
        this.iniciarFormValidator();
    }

    iniciarFormValidator() {
        this.conductorForm = this.fb.group({
            'id': new FormControl(null),
            'nombre': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_NOT_EMPTY.source)]),
            'apellidoP': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_NOT_EMPTY.source)]),
            'apellidoM': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_NOT_EMPTY.source)]),
            'fechaNac': new FormControl(null, [Validators.required]),
            'direccion': new FormControl(null),
            'telefono': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_TELEFONO.source)]),
            'dni': new FormControl(null, [Validators.required, , Validators.pattern(REGEXP_DNI.source)]),
            'fechaIng': new FormControl(null, [Validators.required]),
            'fechaCes': new FormControl(null),
            'correo': new FormControl(null, [Validators.required, Validators.email]),
            'sexo': new FormControl(null, [Validators.required]),
            'empresa': new FormControl(null, [Validators.required]),
            'cargo': new FormControl(null, [Validators.required]),
            'estado': new FormControl(null, [Validators.required]),
            'area': new FormControl(null, [Validators.required]),
            'regPen': new FormControl(null, [Validators.required]),
            'comision': new FormControl(null, [Validators.required]),
            'cussp': new FormControl(null),
            'rol': new FormControl(null, Validators.required),
            'bancoCta': new FormControl(null, [Validators.required]),
            'nroCta': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_CUENTA.source), Validators.pattern(REGEXP_NOT_EMPTY.source)]),
            'bancoCts': new FormControl(null, [Validators.required]),
            'nroCts': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_CUENTA.source), Validators.pattern(REGEXP_NOT_EMPTY.source)]),

            'catBrev': new FormControl(null, Validators.required),
            'brevete': new FormControl(null, Validators.required),
            'fechaBrev': new FormControl(null, Validators.required),
        });
    }

    iniciarComponente() {
        this.accion = this.ACCIONES.NUEVO;
        this.accionNombre = this.getAccionNombre();
        this.verModal = true;
    }

    nuevo() {
        this.resetAllFormFields(this.conductorForm);
        this.msgs = [];
        this.accion = this.ACCIONES.NUEVO;
        this.conductor = new Conductor();
        this.conductor.fechaIngreso = new Date();
        this.conductor.estado = "A";
        this.conductor.idCargo = 7;
        this.conductor.idArea = 3;

        this.accionNombre = this.getAccionNombre();

        this.bloquearPagina();
        this.cargarCombos().then(
            res => {
                this.verModal = true;
                this.conductor.rol = null;
                this.conductor.rol = 2;
                this.desbloquearPagina();
            }
        );
    }
    cargarCombos(): Promise<number> {


        var p1 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_SEXO)).then(
            r1 => {
                this.lstSexo = [];
                this.lstSexo.push({ label: ' -- Seleccionar -- ', value: null });
                r1.forEach(rr1 => this.lstSexo.push({ label: rr1.descripcion, value: rr1.codigoElemento }));
            }
        );

        var p2 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_EMPRESA)).then(
            r2 => {
                this.lstEmpresa = [];
                this.lstEmpresa.push({ label: ' -- Seleccionar -- ', value: null });
                r2.forEach(rr2 => this.lstEmpresa.push({ label: rr2.descripcion, value: rr2.codigoElemento }));
            }
        );

        var p3 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_CARGO)).then(
            r3 => {
                this.lstCargo = [];
                this.lstCargo.push({ label: ' -- Seleccionar -- ', value: null });
                r3.forEach(rr3 => this.lstCargo.push({ label: rr3.descripcion, value: rr3.codigoElemento }));
            }
        );

        var p5 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_AREA)).then(
            r5 => {
                this.lstArea = [];
                this.lstArea.push({ label: ' -- Seleccionar -- ', value: null });
                r5.forEach(rr5 => this.lstArea.push({ label: rr5.descripcion, value: rr5.codigoElemento }));
            }
        );

        var p6 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_REGIMEN_PENSIONARIO)).then(
            r6 => {
                this.lstRegPen = [];
                this.lstRegPen.push({ label: ' -- Seleccionar -- ', value: null });
                r6.forEach(rr6 => this.lstRegPen.push({ label: rr6.descripcion, value: rr6.codigoElemento }));
            }
        );

        var p7 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_COMISION)).then(
            r7 => {
                this.lstCom = [];
                this.lstCom.push({ label: ' -- Seleccionar -- ', value: null });
                r7.forEach(rr7 => this.lstCom.push({ label: rr7.descripcion, value: rr7.codigoElemento }));
            }
        );

        var p8 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_BANCO)).then(
            r8 => {
                this.lstBanco = [];
                this.lstBanco.push({ label: ' -- Seleccionar -- ', value: null });
                r8.forEach(rr8 => this.lstBanco.push({ label: rr8.descripcion, value: rr8.codigoElemento }));
            }
        );

        var p9 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_CATEGORIA_BREVETE)).then(
            r9 => {
                this.lstCategoria = [];
                this.lstCategoria.push({ label: ' -- Seleccionar -- ', value: null });
                r9.forEach(rr9 => this.lstCategoria.push({ label: rr9.descripcion, value: rr9.codigoElemento }));
            }
        );

        var p10 = this.seguridadService.listarRolesActivos().then(
            r10 => {
                this.lstRoles = [];
                this.lstRoles.push({ label: ' -- Seleccionar -- ', value: null });
                r10.forEach(rr10 => this.lstRoles.push({ label: rr10.nombre, value: rr10.rol }));
            }
        );

        return Promise.all([p1, p2, p3, p5, p6, p7, p8, p9, p10]).then(
            res => {
                return 1
            }
        );
    }

    salir() {
        this.verModal = false;
    }

    guardar() {

        this.bloquearPagina();

        this.validateAllFormFields(this.conductorForm);

        if (!this.conductorForm.valid) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error', detail: 'Ingresar correctamente los datos' });
            this.desbloquearPagina();
            return;
        }

        if (this.accion == this.ACCIONES.NUEVO) {
            this.conductorService.ingresar(this.conductor).then(
                res => {
                    if (res.status != 0) {
                        this.enviarMensaje.emit([this.getMensajeRegistrado(res.message)]);
                        this.enviarMostrarDialog.emit('Usuario : ' + res.usuario + ', clave : ' + res.clave);
                        this.enviarListar.emit();
                        this.salir();
                        this.desbloquearPagina();
                    }
                    else {
                        var ms: string[] = res.message.split(';');
                        var trad: Message[] = [];
                        ms.forEach(mm => {
                            this.translateService.get(mm).subscribe(es => trad.push(this.getMensajeError(es)));
                        });
                        this.enviarMensaje.emit(trad);
                        this.desbloquearPagina();
                    }
                }
            );
        }
        else {
            this.conductorService.actualizar(this.conductor).then(
                res => {
                    if (res.status != 0) {
                        this.enviarMensaje.emit([this.getMensajeRegistrado(res.message)]);
                        this.enviarListar.emit();
                        this.salir();
                        this.desbloquearPagina();
                    }
                    else {
                        var ms: string[] = res.message.split(';');
                        var trad: Message[] = [];
                        ms.forEach(mm => {
                            this.translateService.get(mm).subscribe(es => trad.push(this.getMensajeError(es)));
                        });
                        this.enviarMensaje.emit(trad);
                        this.desbloquearPagina();
                    }
                }
            );
        }

    }

    editar(bean: Conductor) {
        this.resetAllFormFields(this.conductorForm);
        this.msgs = [];
        this.accion = this.ACCIONES.EDITAR;
        this.accionNombre = this.getAccionNombre();
        this.conductor = new Conductor();
        this.verModal = true;
        this.bloquearPagina();

        this.cargarCombos().then(
            res => {
                this.conductorService.obtenerPorId(bean.idEmpleado).then(
                    re => {
                        this.conductor = re;

                        this.conductor.fechaNacim = this.conductor.fechaNacim == undefined ? null : this.conductor.fechaNacim == null ? null : new Date(this.conductor.fechaNacim);
                        this.conductor.fechaIngreso = this.conductor.fechaIngreso == undefined ? null : this.conductor.fechaIngreso == null ? null : new Date(this.conductor.fechaIngreso);
                        this.conductor.fechaCese = this.conductor.fechaCese == undefined ? null : this.conductor.fechaCese == null ? null : new Date(this.conductor.fechaCese);
                        this.conductor.fechaVencimiento = this.conductor.fechaVencimiento == undefined ? null : this.conductor.fechaVencimiento == null ? null : new Date(this.conductor.fechaVencimiento);
                        
                        this.desbloquearPagina();
                    }
                );
            }
        );
    }

}



