import { SelectorComponent } from './../../comun/selector.component';
import { TABLA_TIPO_CATEGORIA_VEHICULO, PAGINACION_CANTIDAD_DEVOLVER } from './../constantes';
import { MiscelaneoDetalle } from './../miscelaneo/miscelaneoDetalle';
import { MiscelaneoService } from './../miscelaneo/miscelaneo.service';
import { VehiculoService } from './vehiculo-service';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { LazyLoadEvent, SelectItem } from 'primeng/primeng';
import { OnInit, Component, EventEmitter, Output } from '@angular/core';
import { BaseComponent } from './../../comun/basecomponent';
import { Vehiculo } from './vehiculo';

@Component({
    selector: 'vehiculo-selector',
    templateUrl: './vehiculo-selector.component.html'
})
export class VehiculoSelectorComponent extends SelectorComponent {

    @Output() block = new EventEmitter();
    @Output() unBlock = new EventEmitter();
    @Output() cargarSeleccionEvent = new EventEmitter();

    lstCategoria: SelectItem[] = [];

    paginacion: ParametroPaginacion = new ParametroPaginacion();
    filtro: Vehiculo = new Vehiculo();
    seleccionado: Vehiculo;


    verSelector: boolean = false;

    constructor(private vehiculoService: VehiculoService, private miscelaneoService: MiscelaneoService) {
        super();
    }

    iniciarComponente() {
        this.cargarCategorias();
    }

    cargarCategorias() {
        this.block.emit();
        var filtroTipoReserva = new MiscelaneoDetalle();
        filtroTipoReserva.codigoTabla = TABLA_TIPO_CATEGORIA_VEHICULO;
        this.lstCategoria = [];
        this.seleccionado = null;
        this.lstCategoria.push({ label: ' -- Todos -- ', value: null });
        this.miscelaneoService.listado(filtroTipoReserva).then(
            res => {
                res.forEach(reg => this.lstCategoria.push({ label: reg.descripcion, value: reg.codigoElemento }));
                this.filtro.idCategoria = null;
                this.listarDefecto();
            }
        );

    }
    listarDefecto() {
        this.paginacion.registroInicio = 0;
        this.paginacion.cantidadRegistrosDevolver = this.registrosDevolver;
        this.vehiculoService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                this.paginacion = pg;
                this.unBlock.emit();
                this.verSelector = true;
            });
    }
    salir() {
        this.verSelector = false;
    }

    onRowSelect(event: any) {
        this.cargarSeleccionEvent.emit(event.data);
    }

    buscar(datatable?: any) {
        datatable.reset();
    }
    cargarVehiculos(event: LazyLoadEvent) {
        if (!this.verSelector) {
            return;
        }
        this.block.emit();
        this.paginacion.listaResultado = [];
        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = event.rows;
        this.vehiculoService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                this.paginacion = pg;
                this.unBlock.emit();
            });
    }


}



