import { DataTable } from 'primeng/components/datatable/datatable';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { RecursoTrasladoMantenimientoComponent } from './recurso-traslado-mantenimiento.component';
import { TrasladoDetalle } from './../traslado/trasladodetalle';
import { TrasladoService } from './../traslado/traslado.service';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Traslado } from './../traslado/traslado';
import { ClienteSelectorComponent } from './../cliente/cliente-selector.component';
import { Vehiculo } from './../vehiculo/vehiculo';
import { Conductor } from './../conductor/conductor';
import { Component, OnInit, ViewChild, EventEmitter, Output } from '@angular/core';
import { SelectItem, LazyLoadEvent } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';
import { FiltroAsignacion } from './filtroasginacion';
import { VehiculoSelectorComponent } from '../vehiculo/vehiculo-selector.component';
import { DtoConductor } from '../conductor/dtoconductor';
import { Cliente } from '../cliente/cliente';
import { ConductorSelectorComponent } from '../conductor/conductor-selector.component';

@Component({
    selector: 'recurso-traslado',
    templateUrl: './recurso-traslado.component.html'
})
export class RecursoTrasladoComponent extends BaseComponent implements OnInit {

    @ViewChild(RecursoTrasladoMantenimientoComponent) recursoTrasladoMantenimientoComponent: RecursoTrasladoMantenimientoComponent;
    @Output() cargarListadoEvent = new EventEmitter();

    traslado: Traslado = new Traslado();
    lstDetalle: TrasladoDetalle[] = [];
    verModal: boolean = false;
    cantidadOcupada: number = 0;

    constructor(private trasladoService: TrasladoService) {
        super();
    }

    ngOnInit() {
    }


    iniciarComponente() {
        this.verModal = true;
        this.listar();
    }

    listar() {
        this.bloquearPagina();
        this.trasladoService.obtenerDetalle(this.traslado.nroTraslado).then(
            res => {
                this.lstDetalle = res;
                this.cantidadOcupada = 0;
                this.lstDetalle.forEach(
                    r => {
                        this.cantidadOcupada += r.capacidad;
                    }
                );
                this.traslado.libre = this.traslado.capacidad - this.cantidadOcupada;
                this.desbloquearPagina();
            }
        );
    }

    listarPor2() {
        this.listar();
        console.log('listando atras 1');
        this.cargarListadoEvent.emit();
    }

    salir() {
        this.verModal = false;
    }

    editar(det: TrasladoDetalle) {
        this.recursoTrasladoMantenimientoComponent.iniciarComponente();
        this.recursoTrasladoMantenimientoComponent.editar(det);
    }

    agregar() {
        this.recursoTrasladoMantenimientoComponent.traslado = this.traslado;
        this.recursoTrasladoMantenimientoComponent.iniciarComponente();
        this.recursoTrasladoMantenimientoComponent.nuevo();
    }

}



