import { MiscelaneoService } from './../miscelaneo/miscelaneo.service';
import { TABLA_TIPO_MARCA_VEHICULO } from './../constantes';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { VechiculoMantenimientoComponent } from './vehiculo-mantenimiento.component';
import { VehiculoService } from './vehiculo-service';
import { Vehiculo } from './vehiculo';
import { Component, OnInit, ViewChild } from '@angular/core';
import { SelectItem, LazyLoadEvent, ConfirmationService } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';

@Component({
    templateUrl: './vehiculo-listado.component.html'
})
export class VehiculoListadoComponent extends BaseComponent implements OnInit {

    @ViewChild(VechiculoMantenimientoComponent) vechiculoMantenimientoComponent: VechiculoMantenimientoComponent;
    filtro: Vehiculo = new Vehiculo();
    lstMarca: SelectItem[] = [];
    paginacion: ParametroPaginacion = new ParametroPaginacion();

    constructor(private vehiculoService: VehiculoService, private miscelaneoService: MiscelaneoService, private confirmationService: ConfirmationService) {
        super();
    }

    ngOnInit() {
        this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_MARCA_VEHICULO)).then(
            r3 => {
                this.lstMarca = [];
                this.lstMarca.push({ label: ' -- Todos -- ', value: null });
                r3.forEach(rr3 => this.lstMarca.push({ label: rr3.descripcion, value: rr3.codigoElemento }));
            }
        );
    }
    buscar(dt: any) {
        dt.reset();
    }
    nuevo(dt: any) {
        this.tb = dt;
        this.vechiculoMantenimientoComponent.tb = dt;
        this.vechiculoMantenimientoComponent.nuevo();
    }

    cargarVehiculos(event: LazyLoadEvent) {
        this.bloquearPagina();
        this.paginacion.listaResultado = [];
        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = event.rows;
        this.vehiculoService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                this.paginacion = pg;
                this.desbloquearPagina();
            });
    }

    editar(bean: Vehiculo, dt: any) {
        this.tb = dt;
        this.vechiculoMantenimientoComponent.editar(bean);
    }

    anular(bean: Vehiculo, dt: any) {

        this.confirmationService.confirm({
            header: 'Confirmacin',
            message: 'Desea inactivar el registro?',
            key: 'inactivar',
            accept: () => {
                this.bloquearPagina();
                this.tb = dt;
                this.vehiculoService.obtenerPorId(bean.id).then(
                    re => {
                        console.log(re);
                        re.estado = 'I';
                        this.vehiculoService.actualizar(re).then(
                            re => {
                                dt.reset();
                            }
                        );
                    }
                );
            }
        });
    }

}



