import { DataTable } from 'primeng/components/datatable/datatable';
import { RecursoSelectorComponent } from './recurso-selector.component';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { RecursoTrasladoComponent } from './recurso-traslado.component';
import { TrasladoService } from './../traslado/traslado.service';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Traslado } from './../traslado/traslado';
import { ClienteSelectorComponent } from './../cliente/cliente-selector.component';
import { Vehiculo } from './../vehiculo/vehiculo';
import { Conductor } from './../conductor/conductor';
import { Component, OnInit, ViewChild } from '@angular/core';
import { SelectItem, LazyLoadEvent } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';
import { FiltroAsignacion } from './filtroasginacion';
import { VehiculoSelectorComponent } from '../vehiculo/vehiculo-selector.component';
import { DtoConductor } from '../conductor/dtoconductor';
import { Cliente } from '../cliente/cliente';
import { ConductorSelectorComponent } from '../conductor/conductor-selector.component';

@Component({
    templateUrl: './asignacion-listado.component.html'
})
export class AsignacionListadoComponent extends BaseComponent implements OnInit {

    @ViewChild(ClienteSelectorComponent) clienteSelectorComponent: ClienteSelectorComponent;
    @ViewChild(RecursoTrasladoComponent) recursoTrasladoComponent: RecursoTrasladoComponent;
    @ViewChild(DataTable) dataTableComponent: DataTable;

    cliente: Cliente = new Cliente();
    filtro: Traslado = new Traslado();
    paginacion: ParametroPaginacion = new ParametroPaginacion();

    constructor(private trasladoService: TrasladoService) {
        super();
    }

    ngOnInit() {
        this.bloquearPagina();
        this.filtro.fecha = new Date();
        this.filtro.auxReserva = null;
        this.filtro.estado = 'P';
        this.filtro.fecha.setDate(this.filtro.fecha.getDate() + 1);
    }

    buscar(dt: any) {
        dt.reset();
    }

    listar() {
        console.log("listado sup");
        this.filtro.fecha = new Date();
        this.filtro.fecha.setDate(this.filtro.fecha.getDate() + 1);
        this.dataTableComponent.reset();
    }

    iniciarSelectorCliente() {
        this.clienteSelectorComponent.iniciarComponente();
    }

    cargarTraslados(event: LazyLoadEvent) {
        this.bloquearPagina();
        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = event.rows;
        this.trasladoService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                this.paginacion = pg;
                this.desbloquearPagina();
            });
    }

    limpiarCliente() {
        this.cliente.id = null;
        this.cliente.razonSocial = null;
        this.filtro.cliente = null;
    }
    cargarCliente(dtoCliente: Cliente) {
        this.cliente = dtoCliente;
        this.filtro.cliente = dtoCliente.id;
        this.clienteSelectorComponent.salir();
    }

    verRecursos(dto: Traslado) {
        this.recursoTrasladoComponent.traslado = dto;
        this.recursoTrasladoComponent.iniciarComponente();
    }

    cargarRecurso(res: any) {
        console.log(res);
    }

}



