import { PlanService } from './../plan/plan.service';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { ClienteService } from './../cliente/cliente.service';
import { ReservaService } from './reserva.service';
import { ClienteSelectorComponent } from './../cliente/cliente-selector.component';
import { Plan } from './../plan/plan';
import { SelectItem, ConfirmationService } from 'primeng/primeng';
import { TABLA_TIPO_RESERVA } from './../constantes';
import { MiscelaneoDetalle } from './../miscelaneo/miscelaneoDetalle';
import { MiscelaneoService } from './../miscelaneo/miscelaneo.service';
import { OnInit, Component, ViewChild } from '@angular/core';
import { BaseComponent } from './../../comun/basecomponent';
import { Router, ActivatedRoute } from '@angular/router';
import { Reserva } from './reserva';
import { Cliente } from '../cliente/cliente';

@Component({
    selector: 'reserva-mantenimiento',
    templateUrl: './reserva-mantenimiento.component.html'
})
export class ReservaMantenimientoComponent extends BaseComponent implements OnInit {

    @ViewChild(ClienteSelectorComponent) clienteSelectorComponent: ClienteSelectorComponent;

    accionNombre: string = "";
    lstTipoReserva: SelectItem[] = [];
    lstTipoPlan: SelectItem[] = [];
    planActual: Plan = new Plan();
    reserva: Reserva = new Reserva();
    cliente: Cliente = new Cliente();
    agrega: boolean = true;
    grabaPlan: boolean = true;

    anularTrasladosEjecutados: boolean = false;
    verModalAnular: boolean = false;

    constructor(
        private planService: PlanService,
        private clienteService: ClienteService,
        private reservaService: ReservaService,
        private confirmationService: ConfirmationService,
        private router: Router,
        private route: ActivatedRoute,
        private miscelaneoService: MiscelaneoService) {
        super();
    }

    ngOnInit() {
        this.bloquearPagina();
        this.cargarCombos();
        this.ini();
    }

    cargarCombos() {

        var filtroTipoReserva = new MiscelaneoDetalle();
        filtroTipoReserva.codigoTabla = TABLA_TIPO_RESERVA;

        this.lstTipoReserva.push({ label: ' -- Seleccionar -- ', value: null });
        this.miscelaneoService.listado(filtroTipoReserva).then(
            res => {
                res.forEach(reg => this.lstTipoReserva.push({ label: reg.descripcion, value: reg.codigoElemento }));
            }
        );
        this.lstTipoPlan.push({ label: ' -- Seleccionar -- ', value: 0 });
        this.lstTipoPlan.push({ label: 'Ida', value: 1 });
        this.lstTipoPlan.push({ label: 'Ida y Retorno', value: 2 });
    }

    ini() {
        let reserva = this.route.snapshot.params['reserva'];

        if (reserva) {
            this.editar();
            this.nuevoPlan();
        }
        else {
            this.nuevo();
        }
    }

    nuevo() {
        this.horaMin = new Date();
        this.horaMax = new Date();

        this.horaMin.setHours(20);
        this.horaMin.setMinutes(0);

        this.horaMax.setHours(22);
        this.horaMax.setMinutes(0);
        this.accion = this.ACCIONES.NUEVO;
        this.accionNombre = this.getAccionNombre();
        this.reserva = new Reserva();
        this.nuevoPlan();
        this.desbloquearPagina();
    }
    editar() {
        this.accion = this.ACCIONES.EDITAR;
        this.accionNombre = this.getAccionNombre();


        this.reservaService.obtenerPorId(this.route.snapshot.params['reserva']).then(
            res => {
                this.reserva = res;
                this.reserva.fechaInicio = this.reserva.fechaInicio == null ? null : this.reserva.fechaInicio == undefined ? null : new Date(this.reserva.fechaInicio);
                this.reserva.fechaFin = this.reserva.fechaFin == null ? null : this.reserva.fechaFin == undefined ? null : new Date(this.reserva.fechaFin);


                console.log(this.reserva.lstPlan);

                this.reserva.lstPlan.forEach(
                    reg => {
                        reg.horaDestinoIda = reg.horaDestinoIda == null ? null : reg.horaDestinoIda == undefined ? null : new Date(reg.horaDestinoIda);
                        reg.horaDestinoVuelta = reg.horaDestinoVuelta == null ? null : reg.horaDestinoVuelta == undefined ? null : new Date(reg.horaDestinoVuelta);
                        reg.horaPartidaIda = reg.horaPartidaIda == null ? null : reg.horaPartidaIda == undefined ? null : new Date(reg.horaPartidaIda);
                        reg.horaPartidaVuelta = reg.horaPartidaVuelta == null ? null : reg.horaPartidaVuelta == undefined ? null : new Date(reg.horaPartidaVuelta);
                    }
                );


                this.obtenerCliente();
            }
        );



    }
    obtenerCliente() {
        if (this.reserva.cliente == null) {
            this.desbloquearPagina();
            return null;
        }

        this.clienteService.obtenerPorId(this.reserva.cliente).then(
            res => {
                this.cliente = res;
                this.desbloquearPagina();
            }
        );


    }

    salir() {
        this.router.navigate(["mavitours/reserva-listado"]);
    }

    guardar(bk: any) {

        if (!this.validar()) {
            return;
        }

        this.bloquearPagina();

        var copia = this.copiar(this.reserva);

        for (var i: number = 0; i < copia.lstPlan.length; i++) {
            copia.lstPlan[i] = this.copiar(this.reserva.lstPlan[i]);
        }

        if (this.accion == this.ACCIONES.NUEVO) {
            this.reservaService.ingresar(copia).then(
                res => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: 'Informacin', detail: res });
                    bk.blocked = false;
                    this.salir();
                }
            );
        }
        else {
            this.reservaService.actualizar(copia).then(
                res => {
                    this.msgs = [];
                    this.msgs.push({ severity: 'success', summary: 'Informacin', detail: res });
                    bk.blocked = false;
                    this.salir();
                }
            );
        }

    }
    validar(): boolean {
        var valida = true;
        var rangoValida = true;

        this.msgs = [];
        if (this.reserva.tipoReserva == null) {
            this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Seleccionar un tipo de reserva" });
            valida = false;
        }
        if (this.reserva.fechaInicio == null) {
            this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Ingresar una fecha de inicio" });
            valida = false;
            rangoValida = false;
        }
        if (this.reserva.fechaFin == null) {
            this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Ingresar una fecha de fin" });
            valida = false;
            rangoValida = false;
        }
        if (this.reserva.cliente == null) {
            this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Seleccionar un cliente" });
            valida = false;
        }

        if (rangoValida) {
            if (this.reserva.fechaInicio > this.reserva.fechaFin) {
                this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Ingresar fechas coherentes en el TIEMPO DE CONTRATO" });
                valida = false;
            }
        }

        if (this.reserva.lstPlan.length == 0) {
            this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Ingresar como mnimo un plan" });
            valida = false;
        }

        return valida;
    }
    iniciarSelectorCliente() {
        this.clienteSelectorComponent.iniciarComponente();
    }
    cargarCliente(dtoCliente: Cliente) {
        this.reserva.cliente = dtoCliente.id;
        this.cliente = dtoCliente;
        this.clienteSelectorComponent.salir();
    }

    guardarPlan() {

        if (!this.validarPlan()) {
            return;
        }

        var temp = [...this.reserva.lstPlan];
        this.planActual.tipoPlanNombre = this.planActual.tipoPlan == 1 ? 'Ida' : 'Ida y Retorno';
        this.planActual.dias = this.calcularDias(this.planActual);

        let aux = this.reserva.lstPlan.find(obj => obj.auxSecuencia == this.planActual.auxSecuencia);
        let index = this.reserva.lstPlan.indexOf(aux);

        temp[index] = this.planActual;
        this.reserva.lstPlan = temp;
        this.nuevoPlan();

    }
    agregarPlan() {

        if (!this.validarPlan()) {
            return;
        }

        var temp = [...this.reserva.lstPlan];
        this.planActual.tipoPlanNombre = this.planActual.tipoPlan == 1 ? 'Ida' : 'Ida y Retorno';
        this.planActual.dias = this.calcularDias(this.planActual);
        this.planActual.auxSecuencia = this.calcularSecuencia();


        temp.push(this.planActual);

        this.reserva.lstPlan = temp;
        this.nuevoPlan();


    }

    validarPlan(): boolean {
        this.msgs = [];
        var valida = true;
        var validaIda = true;
        var validaVuelta = true;
        var rangoIda = true;
        var rangoVuelta = true;

        if (this.planActual.capacidad == null || this.planActual.capacidad == 0) {
            this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Ingresar una capacidad mayor a 0" });
            valida = false;
        }

        if (!this.planActual.lunes && !this.planActual.martes && !this.planActual.miercoles &&
            !this.planActual.jueves && !this.planActual.viernes && !this.planActual.sabado &&
            !this.planActual.domingo
        ) {
            this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Seleccionar como mnimo un da" });
            valida = false;
        }

        if (this.planActual.puntoPartidaIda == undefined || this.planActual.puntoPartidaIda.trim() == '') {
            validaIda = false;
        }
        if (this.planActual.horaPartidaIda == null) {
            validaIda = false;
            rangoIda = false;
        }
        if (this.planActual.puntoDestinoIda == undefined || this.planActual.puntoDestinoIda.trim() == '') {
            validaIda = false;
        }
        if (this.planActual.horaDestinoIda == null) {
            validaIda = false;
            rangoIda = false;
        }
        else if (this.planActual.horaDestinoIda.getHours() > 21) {
            this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "La hora de destino no puede superar las 21:00 p.m. en la IDA" });
            validaIda = false;
        }
        if (this.planActual.rutaEspecificaIda == undefined || this.planActual.rutaEspecificaIda.trim() == '') {
            validaIda = false;
        }

        if (!validaIda) {
            this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Ingresar los datos de la IDA" });
            valida = false;
        }

        if (rangoIda) {
            if (this.planActual.horaPartidaIda.getHours() * 60 + this.planActual.horaPartidaIda.getMinutes() >= this.planActual.horaDestinoIda.getHours() * 60 + this.planActual.horaDestinoIda.getMinutes() - 1) {
                this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Ingresar horas coherentes en la IDA" });
                valida = false;
            }
        }

        if (this.planActual.tipoPlan == 2) {

            if (this.planActual.puntoPartidaVuelta == undefined || this.planActual.puntoPartidaVuelta.trim() == '') {
                validaVuelta = false;
            }
            if (this.planActual.horaPartidaVuelta == null) {
                rangoVuelta = false;
                validaVuelta = false;
            }
            if (this.planActual.puntoDestinoVuelta == undefined || this.planActual.puntoDestinoVuelta.trim() == '') {
                validaVuelta = false;
            }
            if (this.planActual.horaDestinoVuelta == null) {
                rangoVuelta = false;
                validaVuelta = false;
            }
            else if (this.planActual.horaDestinoVuelta.getHours() > 21) {
                this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "La hora de destino no puede superar las 21:00 p.m. en la VUELTA" });
                validaVuelta = false;
            }
            if (this.planActual.rutaEspecificaVuelta == undefined || this.planActual.rutaEspecificaVuelta.trim() == '') {
                validaVuelta = false;
            }
            if (!validaVuelta) {
                this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Ingresar los datos del RETORNO" });
                valida = false;
            }
            if (rangoVuelta) {
                if (this.planActual.horaPartidaVuelta >= this.planActual.horaDestinoVuelta) {
                    this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Ingresar horas coherentes en el RETORNO" });
                    valida = false;
                }
                if (rangoVuelta) {
                    if (this.planActual.horaDestinoIda > this.planActual.horaPartidaVuelta) {
                        this.msgs.push({ severity: 'error', summary: 'Informacin', detail: "Ingresar horas coherentes entre los rangoa de IDA Y RETORNO" });
                        valida = false;
                    }
                }
            }

        }
        else {
            this.planActual.horaDestinoVuelta = null;
            this.planActual.horaPartidaVuelta = null;
            this.planActual.puntoDestinoVuelta = null;
            this.planActual.puntoPartidaVuelta = null;
            this.planActual.rutaEspecificaVuelta = null;
        }

        return valida;
    }

    calcularDias(p: Plan): string {
        var d: string = '';

        if (p.lunes) {
            d += '2,';
        }
        if (p.martes) {
            d += '3,';
        }
        if (p.miercoles) {
            d += '4,';
        }
        if (p.jueves) {
            d += '5,';
        }
        if (p.viernes) {
            d += '6,';
        }
        if (p.sabado) {
            d += '7,';
        }
        if (p.domingo) {
            d += '1,';
        }
        if (d.length > 0) {
            d = d.substring(0, d.length - 1);
        }
        return d;
    }

    horaMin: Date = new Date();
    horaMax: Date = new Date();

    nuevoPlan() {
        this.horaMin = new Date();
        this.horaMax = new Date();

        this.horaMin.setHours(20);
        this.horaMin.setMinutes(0);

        this.horaMax.setHours(22);
        this.horaMax.setMinutes(0);

        this.agrega = true;
        this.grabaPlan = true;
        this.planActual = new Plan();
        this.planActual.tipoPlan = 0;
        this.planActual.capacidad = 0;
        this.planActual.estado = 'P'
        this.planActual.estadoNombre = 'Pendiente';

    }

    editarPlan(p: Plan) {
        this.agrega = false;
        this.grabaPlan = true;
        this.planActual = this.copiar(p);
    }
    verPlan(p: Plan) {
        console.log(p);
        this.grabaPlan = false;
        this.planActual = this.copiar(p);
        this.planActual.horaDestinoIda = this.planActual.horaDestinoIda == null ? null : this.planActual.horaDestinoIda == undefined ? null : new Date(this.planActual.horaDestinoIda);
        this.planActual.horaDestinoVuelta = this.planActual.horaDestinoVuelta == null ? null : this.planActual.horaDestinoVuelta == undefined ? null : new Date(this.planActual.horaDestinoVuelta);
        this.planActual.horaPartidaIda = this.planActual.horaPartidaIda == null ? null : this.planActual.horaPartidaIda == undefined ? null : new Date(this.planActual.horaPartidaIda);
        this.planActual.horaPartidaVuelta = this.planActual.horaPartidaVuelta == null ? null : this.planActual.horaPartidaVuelta == undefined ? null : new Date(this.planActual.horaPartidaVuelta);
    }
    eliminarPlan(p: Plan) {

        this.confirmationService.confirm({
            header: 'Confirmacin',
            message: 'Desea remover el registro?',
            key: 'eliminarplan',
            accept: () => {
                let aux = p;
                let index = this.reserva.lstPlan.indexOf(aux);
                this.reserva.lstPlan = this.reserva.lstPlan.filter((val, i) => i != index);
                this.nuevoPlan();
            }
        });
    }
    calcularSecuencia(): number {
        if (this.reserva.lstPlan.length == 0) {
            return 1;
        }
        var max: number = this.reserva.lstPlan[0].auxSecuencia;

        this.reserva.lstPlan.forEach(
            reg => {
                if (reg.auxSecuencia > max) {
                    max = reg.auxSecuencia;
                }
            }
        );

        return max + 1;

    }


    anularPlan(bean: Plan) {
        this.confirmationService.confirm({
            header: 'Confirmacin',
            message: 'Desea anular el registro?',
            key: 'eliminarplan',
            accept: () => {
                this.iniciarModalAnular(bean);
            }
        });
    }

    planSeleccionado: Plan;

    iniciarModalAnular(bean: Plan) {
        this.planSeleccionado = bean;
        this.anularTrasladosEjecutados = false;
        this.verModalAnular = true;
    }

    aceptarAnular() {
        this.bloquearPagina();

        this.planSeleccionado.trasladosEjecutados = this.anularTrasladosEjecutados;

        this.planService.anular(this.planSeleccionado).then(
            res => {
                this.verModalAnular = false;
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: 'Informacin', detail: res });
                this.planSeleccionado.estado = 'X';
                this.planSeleccionado.estadoNombre = 'Anulado';
                var temp = [...this.reserva.lstPlan];
                temp = temp.filter(r => r.numero != this.planSeleccionado.numero);
                temp.push(this.planSeleccionado);
                this.reserva.lstPlan = temp;
                this.desbloquearPagina();
            }
        );
    }
}



