import { Cliente } from './cliente';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Http, Headers, URLSearchParams } from '@angular/http';
import { Injectable } from '@angular/core';
import { REST_URL } from '../constantes';

@Injectable()
export class ClienteService {

    private url = REST_URL + '/api/clientes/';
    private headers = new Headers({ 'Content-Type': 'application/json' });

    constructor(private http: Http) { }

    listarConPaginacion(filtro: Cliente, paginacion: ParametroPaginacion): Promise<ParametroPaginacion> {

        var params = new URLSearchParams();
        params.set("paginacion", JSON.stringify(paginacion));
        params.set("filtro", JSON.stringify(filtro));

        return this.http.get(`${this.url}listarconpaginacion`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.paginacion as ParametroPaginacion;
            }
            );
    }
    obtenerPorId(pk: number) {
        var params = new URLSearchParams();
        params.set('pk', JSON.stringify(pk));

        return this.http.get(`${this.url}obtenerporid`, { search: params }).toPromise().then(
            response => {
                return response.json().res as Cliente;
            }
        );

    }

    actualizar(bean: Cliente): Promise<any> {
        return this.http.post(`${this.url}actualizar`, bean, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo;
            }
        ).catch(
            res => {
                var respo = res.json();
                return respo;
            }
            );
    }

    ingresar(cliente: Cliente): Promise<any> {

        return this.http.post(`${this.url}ingresar`, cliente, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo;
            }
        ).catch(
            res => {
                var respo = res.json();
                return respo;
            }
            );

    }
}



