import { REGEXP_NOT_EMPTY } from './../constantes';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { state } from '@angular/animations';
import { MiscelaneoDetalle } from './miscelaneoDetalle';
import { MiscelaneoService } from './miscelaneo.service';
import { SelectItem } from 'primeng/primeng';
import { OnInit, Component, Output, EventEmitter } from '@angular/core';
import { BaseComponent } from './../../comun/basecomponent';

@Component({
    selector: 'miscelaneo-mantenimiento',
    templateUrl: './miscelaneo-mantenimiento.component.html'
})
export class MiscelaneoMantenimientoComponent extends BaseComponent implements OnInit {

    accionNombre: string = "";
    verModal: boolean = false;
    lstTablas: SelectItem[] = [];
    lstEstado: SelectItem[] = [];
    miscelaneo: MiscelaneoDetalle = new MiscelaneoDetalle();
    miscelaneoForm: FormGroup;
    @Output() listarEvent = new EventEmitter();

    constructor(private miscelaneoService: MiscelaneoService, private fb: FormBuilder) {
        super();
    }

    ngOnInit() {
        this.iniciarFormValidator();
    }

    iniciarFormValidator() {
        this.miscelaneoForm = this.fb.group({
            'codigo': new FormControl(null),
            'descripcion': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_NOT_EMPTY.source)]),
            'tabla': new FormControl(null, [Validators.required]),
            'estado': new FormControl(null, [Validators.required]),
        });
    }

    nuevo() {
        this.resetAllFormFields(this.miscelaneoForm);
        this.msgs = [];
        this.accion = this.ACCIONES.NUEVO;
        this.accionNombre = this.getAccionNombre();
        this.miscelaneo = new MiscelaneoDetalle();
        this.miscelaneo.estado = 'A';
        this.verModal = true;
        this.bloquearPagina();

        this.lstTablas = [];
        this.lstTablas.push({ label: ' -- Seleccionar -- ', value: null });
        this.miscelaneoService.listarTablas().then(
            response => {
                response.forEach(reg => this.lstTablas.push({ label: reg.nombre, value: reg.codigoTabla }));
                this.desbloquearPagina();
            });
        this.lstEstado = [];
        this.lstEstado.push({ label: " -- Seleccionar -- ", value: null });
        this.lstEstado.push({ label: 'Activo', value: 'A' });
        this.lstEstado.push({ label: 'Inactivo', value: 'I' });

    }

    editar(bean: MiscelaneoDetalle) {
        this.resetAllFormFields(this.miscelaneoForm);
        this.miscelaneo = new MiscelaneoDetalle();
        this.msgs = [];
        this.accion = this.ACCIONES.EDITAR;
        this.accionNombre = this.getAccionNombre();
        this.verModal = true;
        this.bloquearPagina();

        this.lstEstado = [];
        this.lstEstado.push({ label: " -- Seleccionar -- ", value: null });
        this.lstEstado.push({ label: 'Activo', value: 'A' });
        this.lstEstado.push({ label: 'Inactivo', value: 'I' });

        this.lstTablas = [];
        this.lstTablas.push({ label: ' -- Seleccionar -- ', value: null });
        this.miscelaneoService.listarTablas().then(
            response => {
                response.forEach(reg => {
                    this.lstTablas.push({ label: reg.nombre, value: reg.codigoTabla });
                }
                );

                console.log(this.lstTablas);

                this.miscelaneoService.obtenerPorId(bean).then(
                    res => {
                        console.log(res.codigoTabla);
                        this.miscelaneo = res;
                        this.desbloquearPagina();
                    }
                );


            });
    }

    salir() {
        this.verModal = false;
    }

    guardar() {

        this.bloquearPagina();

        this.validateAllFormFields(this.miscelaneoForm);

        if (!this.miscelaneoForm.valid) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error', detail: 'Ingresar correctamente los datos' });
            this.desbloquearPagina();
            return;
        }

        if (this.accion == this.ACCIONES.NUEVO) {

            this.miscelaneoService.ingresar(this.miscelaneo).then(
                res => {
                    this.enviarMensaje.emit([this.getMensajeRegistrado(res)]);
                    this.desbloquearPagina();
                    this.listarEvent.emit();
                    this.salir();
                }
            );
        }
        else {
            this.miscelaneoService.actualizar(this.miscelaneo).then(
                res => {
                    this.enviarMensaje.emit([this.getMensajeRegistrado(res)]);
                    this.desbloquearPagina();
                    this.listarEvent.emit();
                    this.salir();
                }
            );
        }

    }

}



