import { RouterModule } from '@angular/router/';
import { HomeModule } from './home/home.module';
import { AppComponent } from './app.component';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpModule } from "@angular/http";
import { FormsModule } from "@angular/forms"
import { ShellComponent } from "./shared/shell/shell.component";
import 'rxjs/add/operator/toPromise';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppMenuComponent, AppSubMenu } from './shared/shell/app-menu/app-menu.component';
import { AppRoutes } from './shared/shell/app-routes/app.routes';
import { ConfirmationService, ConfirmDialogModule, DataTableModule, PanelModule, DropdownModule, DialogModule, CalendarModule, ChartModule, GrowlModule, ButtonModule, InputTextModule, BlockUIModule, ConfirmDialog } from 'primeng/primeng';

import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';

import { TranslateService } from '@ngx-translate/core';
import { SeguridadActivator } from './home/seguridad/seguridad.activator';

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [

    PanelModule, ButtonModule, FormsModule,
    InputTextModule, GrowlModule, ChartModule,
    BlockUIModule, CalendarModule, DialogModule,
    DataTableModule, DropdownModule, CalendarModule,
    BrowserModule, FormsModule, BrowserAnimationsModule,

    HomeModule,

    HttpModule,
    RouterModule,
    HttpClientModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    })
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }

export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json', );
}



