import { RUTA_PRINCIPAL } from './../../mavitours/constantes';
import { state } from '@angular/animations';
import { SeguridadService } from './seguridad.service';
import { Injectable } from '@angular/core';
import { Router, CanActivate, ActivatedRoute } from '@angular/router';


@Injectable()
export class SeguridadActivator implements CanActivate {
    constructor(private service: SeguridadService, private router: Router, private r: ActivatedRoute) { }

    canActivate(c: any, d: any) {

        var url: string = d.url.replace(RUTA_PRINCIPAL, '');
        var ind = url.indexOf('/');
        if (ind != -1) {
            url = url.substring(0, ind);
        }

        if (this.service.isLoggedIn()) {
            return this.validarRuta(url).then(
                r => {
                    if (!r) {
                        this.router.navigate(['404']);
                        return false;
                    }
                    else {
                        return true;
                    }
                }
            );

        }
        else {
            console.log(4);
            sessionStorage.clear();
            this.router.navigate(['/login']);
            return false;
        }

    }

    validarRuta(ruta: string): Promise<boolean> {

        return this.service.obtenerOpcionPorUsuario(JSON.parse(sessionStorage.getItem('ua')).usuario, ruta).then(res => {
            var b: boolean = false;
            return res.length != 0;
        });


    }
}


