import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { ConductorMantenimientoComponent } from './conductor-mantenimiento.component';
import { ConductorService } from './conductor.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { SelectItem, LazyLoadEvent, ConfirmationService } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';
import { Conductor } from './conductor';
import { ParametroPaginacion } from '../../home/parametropaginacion';
import { MiscelaneoService } from '../miscelaneo/miscelaneo.service';
import { TABLA_TIPO_CATEGORIA_BREVETE } from '../constantes';

@Component({
    templateUrl: './conductor-listado.component.html'
})
export class ConductorListadoComponent extends BaseComponent implements OnInit {

    @ViewChild(ConductorMantenimientoComponent) conductorMantenimientoComponent: ConductorMantenimientoComponent;
    filtro: Conductor = new Conductor();
    paginacion: ParametroPaginacion = new ParametroPaginacion();
    lstCategoria: SelectItem[] = [];

    constructor(private conductorService: ConductorService, private confirmationService: ConfirmationService, private miscelaneoService: MiscelaneoService) {
        super();
    }

    ngOnInit() {
        this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_CATEGORIA_BREVETE)).then(
            r9 => {
                this.lstCategoria = [];
                this.lstCategoria.push({ label: ' -- Todos -- ', value: null });
                r9.forEach(rr9 => this.lstCategoria.push({ label: rr9.descripcion, value: rr9.codigoElemento }));
            }
        );
    }

    buscar(dt: any) {
        console.log('buscando');
        dt.reset();
    }

    nuevo(dt: any) {
        this.tb = dt;
        this.conductorMantenimientoComponent.tb = dt;
        this.conductorMantenimientoComponent.nuevo();
    }

    mostrarDialogPre(mensaje: string) {
        this.mostrarDialogInformacion(mensaje, this.confirmationService);
    }

    cargarConductores(event: LazyLoadEvent) {
        this.bloquearPagina();
        this.paginacion.listaResultado = [];
        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = event.rows;
        this.conductorService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                this.paginacion = pg;
                this.desbloquearPagina();
            });
    }

    editar(bean: Conductor, dt: any) {
        this.tb = dt;
        this.conductorMantenimientoComponent.editar(bean);
    }

    anular(bean: Conductor, dt: any) {

        this.confirmationService.confirm({
            header: 'Confirmacin',
            message: 'Desea inactivar el registro?',
            key: 'inactivar',
            accept: () => {
                this.bloquearPagina();
                this.tb = dt;
                this.conductorService.obtenerPorId(bean.idEmpleado).then(
                    re => {
                        re.estado = 'I';
                        this.conductorService.actualizar(re).then(
                            re => {
                                dt.reset();
                            }
                        );
                    }
                );
            }
        });
    }
}



