import { ParametroPaginacion } from './../../home/parametropaginacion';
import { TABLA_TIPO_CLIENTE } from './../constantes';
import { MiscelaneoDetalle } from './../miscelaneo/miscelaneoDetalle';
import { MiscelaneoService } from './../miscelaneo/miscelaneo.service';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { ClienteMantenimientoComponent } from './cliente-mantenimiento.component';
import { ClienteService } from './cliente.service';
import { Cliente } from './cliente';
import { Component, OnInit, ViewChild } from '@angular/core';
import { SelectItem, LazyLoadEvent, Message, ConfirmationService } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';

@Component({
    templateUrl: './cliente-listado.component.html'
})
export class ClienteListadoComponent extends BaseComponent implements OnInit {

    @ViewChild(ClienteMantenimientoComponent) clienteMantenimientoComponent: ClienteMantenimientoComponent;
    filtro: Cliente = new Cliente();
    lstTipo: SelectItem[] = [];
    paginacion: ParametroPaginacion = new ParametroPaginacion();

    constructor(private clienteService: ClienteService, private miscelaneoService: MiscelaneoService, private confirmationService: ConfirmationService) {
        super();
    }

    ngOnInit() {
        this.lstTipo = [];
        this.lstTipo.push({ label: " -- Todos -- ", value: null });
        var tipo: MiscelaneoDetalle = new MiscelaneoDetalle();
        tipo.estado = 'A';
        tipo.codigoTabla = TABLA_TIPO_CLIENTE;
        this.miscelaneoService.listado(tipo).then(
            res => {
                res.forEach(reg => this.lstTipo.push({ label: reg.descripcion, value: reg.codigoElemento }));
                this.desbloquearPagina();
            }
        );
    }


    buscar(dt: any) {
        dt.reset();
    }
    nuevo(dt: any) {
        this.tb = dt;
        this.clienteMantenimientoComponent.tb = dt;
        this.clienteMantenimientoComponent.nuevo();
    }

    cargarClientes(event: LazyLoadEvent) {
        this.bloquearPagina();
        this.paginacion.listaResultado = [];
        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = event.rows;
        this.clienteService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                this.paginacion = pg;
                this.desbloquearPagina();
            });
    }

    editar(bean: Cliente, dt: any) {
        this.tb = dt;
        this.clienteMantenimientoComponent.editar(bean);
    }

    anular(bean: Cliente, dt: any) {

        this.confirmationService.confirm({
            header: 'Confirmacin',
            message: 'Desea inactivar el registro?',
            key: 'inactivar',
            accept: () => {
                this.bloquearPagina();
                this.tb = dt;
                this.clienteService.obtenerPorId(bean.id).then(
                    re => {
                        console.log(re);
                        re.estado = 'I';
                        this.clienteService.actualizar(re).then(
                            re => {
                                dt.reset();
                            }
                        );
                    }
                );
            }
        });
    }

}



