import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Reserva } from './reserva';
import { Http, Headers, URLSearchParams, Response } from '@angular/http';
import { Injectable } from '@angular/core';
import { REST_URL } from '../constantes';

@Injectable()
export class ReservaService {

    private url = REST_URL + '/api/reservas/';
    private headers = new Headers({ 'Content-Type': 'application/json' });

    constructor(private http: Http) { }

    listarConPaginacion(filtro: Reserva, paginacion: ParametroPaginacion): Promise<ParametroPaginacion> {

        var params = new URLSearchParams();
        params.set("paginacion", JSON.stringify(paginacion));
        params.set("filtro", JSON.stringify(filtro));

        return this.http.get(`${this.url}listarconpaginacion`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.paginacion as ParametroPaginacion;
            }
            );
    }

    ingresar(bean: Reserva): Promise<string> {

        this.cambiarFecha(bean);

        return this.http.post(`${this.url}ingresar`, bean)
            .toPromise().then(response => {
                var p = response.json();
                return p.res as string;
            }
            );
    }

    obtenerPorId(pk: number): Promise<Reserva> {

        var params = new URLSearchParams();
        params.set('pk', JSON.stringify(pk));

        return this.http.get(`${this.url}obtenerporid`, { search: params }).toPromise().then(
            response => {
                return response.json().res as Reserva;
            }
        );

    }
    actualizar(bean: Reserva): Promise<string> {

        this.cambiarFecha(bean);

        return this.http.post(`${this.url}actualizar`, bean)
            .toPromise().then(response => {
                var p = response.json();
                return p.res as string;
            }
            );
    }

    cambiarFecha(bean: Reserva) {

        bean.lstPlan.forEach(reg => {
            if (reg.estado == 'P') {
                reg.horaDestinoIda = this.toDate(reg.horaDestinoIda);
                reg.horaDestinoVuelta = this.toDate(reg.horaDestinoVuelta);
                reg.horaPartidaIda = this.toDate(reg.horaPartidaIda);
                reg.horaPartidaVuelta = this.toDate(reg.horaPartidaVuelta);
            }
        });

    }

    anular(bean: Reserva) {

        return this.http.post(`${this.url}anular`, bean)
            .toPromise().then(response => {
                var p = response.json();
                return p.res as string;
            }
            );
    }

    toDate(date: any): any {

        if (date == null) {
            return null;
        }
        else {
            return '2000-01-01 ' + ((date as Date).toTimeString()).substring(0, 8) + '.000';
        }
    }

}



