import { ContraseniaMantenientoComponent } from './contrasenia/contrasenia-mantenimiento.component';
import { BaseComponent } from './../../comun/basecomponent';
import { Component, ElementRef, Renderer, ViewChild, OnInit } from '@angular/core';

@Component({
  selector: 'app-shell',
  templateUrl: './shell.component.html',
  styles: []
})
export class ShellComponent extends BaseComponent implements OnInit {

  @ViewChild(ContraseniaMantenientoComponent) contraseniaMantenientoComponent: ContraseniaMantenientoComponent;

  menuClick: boolean;

  rightPanelButtonClick: boolean;

  menuButtonClick: boolean;

  topbarMenuButtonClick: boolean;

  topbarMenuClick: boolean;

  topbarMenuActive: boolean;

  activeTopbarItem: Element;

  layoutStatic: boolean;

  rightPanelActive: boolean;

  sidebarActive: boolean;

  mobileMenuActive: boolean;

  constructor(public renderer: Renderer) { super() }

  onWrapperClick() {
    if (!this.menuClick && !this.menuButtonClick) {
      this.mobileMenuActive = false;
    }

    if (!this.topbarMenuClick && !this.topbarMenuButtonClick) {
      this.topbarMenuActive = false;
      this.activeTopbarItem = null;
    }

    if (!this.rightPanelButtonClick) {
      this.rightPanelActive = false;
    }

    this.menuClick = false;
    this.rightPanelButtonClick = false;
    this.menuButtonClick = false;
    this.topbarMenuClick = false;
    this.topbarMenuButtonClick = false;
  }

  onMenuButtonClick(event: Event) {
    this.menuButtonClick = true;

    if (this.isMobile()) {
      this.mobileMenuActive = !this.mobileMenuActive;
    }

    event.preventDefault();
  }

  onTopbarMobileMenuButtonClick(event: Event) {
    this.topbarMenuButtonClick = true;
    this.topbarMenuActive = !this.topbarMenuActive;
    event.preventDefault();
  }

  onRightPanelButtonClick(event: Event) {
    this.rightPanelButtonClick = true;
    this.rightPanelActive = !this.rightPanelActive;
  }

  onTopbarRootItemClick(event: Event, item: Element) {
    if (this.activeTopbarItem === item)
      this.activeTopbarItem = null;
    else
      this.activeTopbarItem = item;

    event.preventDefault();
  }

  onTopbarMenuClick(event: Event) {
    this.topbarMenuClick = true;
  }

  onSidebarClick(event: Event) {
    this.menuClick = true;
  }

  onToggleMenuClick(event: Event) {
    this.layoutStatic = !this.layoutStatic;
  }

  isMobile() {
    return window.innerWidth < 640;
  }
  ngOnInit() {

  }
  showDialog() {
    this.contraseniaMantenientoComponent.iniciarComponente(this.getUsuarioActual().usuario);
  }
}



