import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { routes } from './../../shared/shell/app-routes/app.routes';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { ProveedorSaludMantenimientoComponent } from './proveedorsalud-mantenimiento.component';
import { Component, OnInit, ViewChild } from '@angular/core';
import { SelectItem, LazyLoadEvent, ConfirmationService } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';
import { ProveedorSaludService } from './proveedorsalud.service';
import { ProveedorSalud } from './proveedorsalud';
import { DtoProveedores, DtoProveedoresData } from './dtoproveedores';

@Component({
    templateUrl: './proveedorsalud-listado.component.html'
})
export class ProveedorSaludListadoComponent extends BaseComponent implements OnInit {

    paginacion: ParametroPaginacion = new ParametroPaginacion();
    filtro: ProveedorSalud = new ProveedorSalud();
    @ViewChild(ProveedorSaludMantenimientoComponent) proveedorSaludMantenimientoComponent: ProveedorSaludMantenimientoComponent;

    constructor(private proveedorSaludService: ProveedorSaludService, private confirmationService: ConfirmationService) {
        super();
    }

    ngOnInit() {

    }

    buscar(dt: any) {
        dt.reset();
    }

    cargarProveedores(event: LazyLoadEvent) {

        this.bloquearPagina();

        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = event.rows;
        this.paginacion.listaResultado = [];

        this.proveedorSaludService.listarConPaginacion(this.filtro, this.paginacion).then(
            res => {
                this.desbloquearPagina();
                this.paginacion = res;
            }
        );
    }

    nuevo(datatable: any) {
        this.tb = datatable;
        this.proveedorSaludMantenimientoComponent.tb = this.tb;
        this.proveedorSaludMantenimientoComponent.nuevo();
    }

    editar(bean: ProveedorSalud, dt: any) {
        this.tb = dt;
        this.proveedorSaludMantenimientoComponent.editar(bean);
    }

    anular(bean: ProveedorSalud, dt: any) {

        this.confirmationService.confirm({
            header: 'Confirmacin',
            message: 'Desea inactivar el registro?',
            key: 'inactivar',
            accept: () => {
                this.bloquearPagina();
                this.tb = dt;
                this.proveedorSaludService.obtenerPorId(bean.id).then(
                    re => {
                        re.estado = 'I';
                        this.proveedorSaludService.actualizar(re).then(
                            re => {
                                dt.reset();
                            }
                        );
                    }
                );
            }
        });
    }

}



