import { RecursoSelectorComponent } from './recurso-selector.component';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { TrasladoDetalle } from './../traslado/trasladodetalle';
import { TrasladoService } from './../traslado/traslado.service';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Traslado } from './../traslado/traslado';
import { ClienteSelectorComponent } from './../cliente/cliente-selector.component';
import { Vehiculo } from './../vehiculo/vehiculo';
import { Conductor } from './../conductor/conductor';
import { Component, OnInit, ViewChild, Output, EventEmitter } from '@angular/core';
import { SelectItem, LazyLoadEvent } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';
import { FiltroAsignacion } from './filtroasginacion';
import { VehiculoSelectorComponent } from '../vehiculo/vehiculo-selector.component';
import { DtoConductor } from '../conductor/dtoconductor';
import { Cliente } from '../cliente/cliente';
import { ConductorSelectorComponent } from '../conductor/conductor-selector.component';

@Component({
    selector: 'recurso-traslado-mantenimiento',
    templateUrl: './recurso-traslado-mantenimiento.component.html'
})
export class RecursoTrasladoMantenimientoComponent extends BaseComponent implements OnInit {

    @ViewChild(RecursoSelectorComponent) recursoSelectorComponent: RecursoSelectorComponent;
    @Output() cargarListadoEvent = new EventEmitter();

    verModal: boolean = false;
    traslado: Traslado = new Traslado();
    accionNombre: string = '';
    recurso: TrasladoDetalle = new TrasladoDetalle();
    maximo: number = 0;

    constructor(private trasladoService: TrasladoService) {
        super();
    }

    ngOnInit() {
    }

    guardar() {

        if (this.recurso.conductor == null || this.recurso.vehiculo == null || this.recurso.capacidad == null) {
            this.msgs = [];
            this.msgs.push(this.getMensajeError('Ingresar todos los campos'));
            return;
        }

        this.bloquearPagina();

        console.log(this.recurso);

        this.trasladoService.ingresarDetalle(this.recurso).then(
            res => {
                this.desbloquearPagina();
                this.verModal = false;
                this.cargarListadoEvent.emit();
            }
        );


    }

    cargarRecurso(rec: any) {
        var conductor: Conductor = rec.conductor;
        var vehiculo: Vehiculo = rec.vehiculo;

        this.recurso.conductor = conductor.idEmpleado;
        this.recurso.conductorNombre = conductor.nombreCompleto;

        this.recurso.vehiculo = vehiculo.id;
        this.recurso.vehiculoPlaca = vehiculo.placa;
        this.recurso.vehiculoCapacidad = vehiculo.nroAsientos;
        this.recurso.capacidad = 1;


        this.maximo = this.recurso.vehiculoCapacidad > this.traslado.libre ? this.traslado.libre : this.recurso.vehiculoCapacidad;
    }

    iniciarComponente() {
        this.verModal = true;
    }
    nuevo() {
        this.accion = this.ACCIONES.NUEVO;
        this.accionNombre = this.getAccionNombre();

        this.recurso = new TrasladoDetalle();
        this.recurso.capacidad = 1;
        this.recurso.nroReserva = this.traslado.nroReserva;
        this.recurso.nroPlan = this.traslado.nroPlan;
        this.maximo = this.traslado.libre;
        this.recurso.nroTraslado = this.traslado.nroTraslado;

    }
    editar(reg: TrasladoDetalle) {
        this.recurso.nroDetalleTraslado = reg.nroTraslado;
        this.recurso.conductor = reg.conductor;
        this.recurso.conductorNombre = reg.conductorNombre;
        this.recurso.nroTraslado = reg.nroTraslado;
        this.recurso.vehiculo = reg.vehiculo;
        this.recurso.vehiculoPlaca = reg.vehiculoPlaca;
        this.recurso.capacidad = reg.capacidad;
        this.accion = this.ACCIONES.EDITAR;
        this.accionNombre = this.getAccionNombre();
    }

    salir() {
        this.verModal = false;
    }
    seleccionarRecurso() {
        this.recursoSelectorComponent.traslado = this.traslado;
        this.recursoSelectorComponent.iniciarComponente();
    }

}



