import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { MiscelaneoDetalle } from './miscelaneoDetalle';
import { MiscelaneoMantenimientoComponent } from './miscelaneo-mantenimiento.component';
import { MiscelaneoService } from './miscelaneo.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { SelectItem, LazyLoadEvent, ConfirmationService } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';

@Component({
    templateUrl: './miscelaneo-listado.component.html'
})
export class MiscelaneoListadoComponent extends BaseComponent implements OnInit {

    @ViewChild(MiscelaneoMantenimientoComponent) miscelaneoMantenimientoComponent: MiscelaneoMantenimientoComponent;
    filtro: MiscelaneoDetalle = new MiscelaneoDetalle();
    lstMiscelaneo: MiscelaneoDetalle[] = [];
    lstTablas: SelectItem[] = [];
    lstEstados: SelectItem[] = [];


    constructor(private miscelaneoService: MiscelaneoService, private confirmationService: ConfirmationService) {
        super();
    }

    ngOnInit() {
        this.filtro.codigoElemento = null;
        this.filtro.descripcion = '';
        this.cargarTablas();
        this.cargarEstados();
    }

    cargarEstados() {
        this.lstEstados.push({ label: ' -- Todos -- ', value: '' });
        this.lstEstados.push({ label: ' Activo ', value: 'A' });
        this.lstEstados.push({ label: ' Inactivo ', value: 'I' });
        this.filtro.estado = "A";
    }

    cargarTablas() {
        this.bloquearPagina();
        this.miscelaneoService.listarTablas().then(
            response => {
                response.forEach(reg => this.lstTablas.push({ label: reg.nombre, value: reg.codigoTabla }));
                if (response.length > 0) {
                    this.filtro.codigoTabla = response[0].codigoTabla;
                    this.cargarMiscelaneos();
                }
            }
        );
    }

    buscar() {
        this.cargarMiscelaneos();
    }

    cargarMiscelaneos() {
        this.bloquearPagina();
        this.miscelaneoService.listado(this.filtro).then(
            res => {
                this.lstMiscelaneo = res;
                this.desbloquearPagina();
            }
        );
    }
    nuevo() {
        this.miscelaneoMantenimientoComponent.nuevo();
    }

    enter(event: any) {
        console.log(event);
        if (event.keyCode == 13) {
            this.cargarMiscelaneos();
        }

    }

    editar(bean: MiscelaneoDetalle) {
        this.miscelaneoMantenimientoComponent.editar(bean);
    }
    inactivar(bean: MiscelaneoDetalle) {
        this.confirmationService.confirm({
            header: 'Confirmacin',
            message: 'Desea inactivar el registro?',
            key: 'inactivar',
            accept: () => {
                this.bloquearPagina();
                var temp;
                this.miscelaneoService.obtenerPorId(bean).then(
                    res => {
                        temp = res;
                        temp.estado = 'I';
                        this.miscelaneoService.actualizar(temp).then(
                            res => {
                                this.msgs = [];
                                this.msgs.push({ severity: 'info', summary: 'Informacin', detail: res });
                                this.filtro.estado = '';
                                this.cargarMiscelaneos();
                            }
                        );
                    }
                );
            }
        });
    }
}



