import { EmpleadoService } from './empleado.service';
import { EmpleadoMantenimientoComponent } from './empleado-mantenimiento.component';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { Component, ViewChild } from '@angular/core';
import { SelectItem, LazyLoadEvent, ConfirmationService } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';
import { Empleado } from './empleado';
import { ParametroPaginacion } from '../../home/parametropaginacion';

@Component({
    templateUrl: './empleado-listado.component.html'
})
export class EmpleadoListadoComponent extends BaseComponent {

    @ViewChild(EmpleadoMantenimientoComponent) empleadoMantenimientoComponent: EmpleadoMantenimientoComponent;
    filtro: Empleado = new Empleado();
    paginacion: ParametroPaginacion = new ParametroPaginacion();

    constructor(private empleadoService: EmpleadoService, private confirmationService: ConfirmationService) {
        super();
    }

    buscar(dt: any) {
        dt.reset();
    }


    nuevo(dt: any) {
        this.tb = dt;
        this.empleadoMantenimientoComponent.tb = dt;
        this.empleadoMantenimientoComponent.nuevo();
    }

    mostrarDialogPre(mensaje: string) {
        this.mostrarDialogInformacion(mensaje, this.confirmationService);
    }

    cargarEmpleados(event: LazyLoadEvent) {
        this.bloquearPagina();
        this.paginacion.listaResultado = [];
        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = event.rows;
        this.empleadoService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                this.paginacion = pg;
                this.desbloquearPagina();
            });
    }

    editar(bean: Empleado, dt: any) {
        this.tb = dt;
        this.empleadoMantenimientoComponent.editar(bean);
    }

    anular(bean: Empleado, dt: any) {

        this.confirmationService.confirm({
            header: 'Confirmacin',
            message: 'Desea inactivar el registro?',
            key: 'inactivar',
            accept: () => {
                this.bloquearPagina();
                this.tb = dt;
                this.empleadoService.obtenerPorId(bean.id).then(
                    re => {
                        console.log(re);
                        re.estado = 'I';
                        this.empleadoService.actualizar(re).then(
                            re => {
                                dt.reset();
                            }
                        );
                    }
                );
            }
        });
    }

}



