import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { ClienteService } from './cliente.service';
import { TABLA_TIPO_CLIENTE, REGEXP_CORREO, REGEXP_DNI, REGEXP_RUC, REGEXP_TELEFONO, REGEXP_NOT_EMPTY } from './../constantes';
import { MiscelaneoDetalle } from './../miscelaneo/miscelaneoDetalle';
import { MiscelaneoService } from './../miscelaneo/miscelaneo.service';
import { SelectItem, Message } from 'primeng/primeng';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { OnInit, Component, Output, EventEmitter } from '@angular/core';
import { BaseComponent } from './../../comun/basecomponent';
import { Cliente } from './cliente';
import { TABLA_TIPO_CARGO } from '../constantes';

@Component({
    selector: 'cliente-mantenimiento',
    templateUrl: './cliente-mantenimiento.component.html'
})
export class ClienteMantenimientoComponent extends BaseComponent implements OnInit {

    accionNombre: string = "";
    verModal: boolean = false;

    clienteForm: FormGroup;
    cliente: Cliente = new Cliente();
    lstEstado: SelectItem[] = [];
    lstTipo: SelectItem[] = [];

    constructor(
        private miscelaneoService: MiscelaneoService,
        private clienteService: ClienteService,
        private translateService: TranslateService,
        private fb: FormBuilder
    ) {
        super();
    }

    ngOnInit() {
        this.iniciarFormValidator();
    }

    iniciarFormValidator() {
        this.clienteForm = this.fb.group({
            'id': new FormControl(null),
            'ruc': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_RUC.source)]),
            'tipo': new FormControl(null, [Validators.required]),
            'raz': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_NOT_EMPTY.source)]),
            'dni': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_DNI.source)]),
            'telefono': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_TELEFONO.source)]),
            'estado': new FormControl(null, [Validators.required]),
            'correo': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_CORREO.source)]),
        });
    }

    nuevo() {
        this.resetAllFormFields(this.clienteForm);
        this.msgs = [];
        this.accion = this.ACCIONES.NUEVO;
        this.cliente = new Cliente();
        this.cliente.tipo = null;
        this.cliente.estado = 'A';
        this.lstTipo = [];
        this.lstTipo.push({ label: " -- Seleccionar -- ", value: null });
        this.accionNombre = this.getAccionNombre();
        this.verModal = true;
        this.bloquearPagina();
        var tipo: MiscelaneoDetalle = new MiscelaneoDetalle();
        tipo.estado = 'A';
        tipo.codigoTabla = TABLA_TIPO_CLIENTE;
        this.miscelaneoService.listado(tipo).then(
            res => {
                res.forEach(reg => this.lstTipo.push({ label: reg.descripcion, value: reg.codigoElemento }));
                this.desbloquearPagina();
            }
        );
        this.lstEstado = [];
        this.lstEstado.push({ label: " -- Seleccionar -- ", value: null });
        this.lstEstado.push({ label: 'Activo', value: 'A' });
        this.lstEstado.push({ label: 'Inactivo', value: 'I' });

    }

    salir() {
        this.verModal = false;
    }

    guardar() {

        this.bloquearPagina();

        this.validateAllFormFields(this.clienteForm);

        if (!this.clienteForm.valid) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error', detail: 'Ingresar correctamente los datos' });
            this.desbloquearPagina();
            return;
        }

        if (this.accion == this.ACCIONES.NUEVO) {
            this.clienteService.ingresar(this.cliente).then(
                res => {
                    if (res.status != 0) {
                        this.enviarMensaje.emit([this.getMensajeRegistrado(res.message)]);
                        this.enviarListar.emit();
                        this.salir();
                        this.desbloquearPagina();
                    }
                    else {
                        var ms: string[] = res.message.split(';');
                        var trad: Message[] = [];
                        ms.forEach(mm => {
                            this.translateService.get(mm).subscribe(es => trad.push(this.getMensajeError(es)));
                        });
                        this.enviarMensaje.emit(trad);
                        this.desbloquearPagina();
                    }
                }
            );
        }
        else {
            this.clienteService.actualizar(this.cliente).then(
                res => {
                    if (res.status != 0) {
                        this.enviarMensaje.emit([this.getMensajeRegistrado(res.message)]);
                        this.enviarListar.emit();
                        this.salir();
                        this.desbloquearPagina();
                    }
                    else {
                        var ms: string[] = res.message.split(';');
                        var trad: Message[] = [];
                        ms.forEach(mm => {
                            this.translateService.get(mm).subscribe(es => trad.push(this.getMensajeError(es)));
                        });
                        this.enviarMensaje.emit(trad);
                        this.desbloquearPagina();
                    }
                }
            );
        }
    }

    editar(bean: Cliente) {
        this.resetAllFormFields(this.clienteForm);
        this.msgs = [];
        this.accion = this.ACCIONES.EDITAR;
        this.accionNombre = this.getAccionNombre();
        this.cliente = new Cliente();
        this.verModal = true;
        this.bloquearPagina();

        this.lstEstado = [];
        this.lstEstado.push({ label: " -- Seleccionar -- ", value: null });
        this.lstEstado.push({ label: 'Activo', value: 'A' });
        this.lstEstado.push({ label: 'Inactivo', value: 'I' });

        this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_CLIENTE)).then(
            res => {
                res.forEach(reg => this.lstTipo.push({ label: reg.descripcion, value: reg.codigoElemento }));

                this.clienteService.obtenerPorId(bean.id).then(
                    re => {
                        this.cliente = re;
                        this.desbloquearPagina();
                    }
                );
            }
        );
    }
}



