import { SelectorComponent } from './../../comun/selector.component';
import { MiscelaneoService } from './../miscelaneo/miscelaneo.service';
import { MiscelaneoDetalle } from './../miscelaneo/miscelaneoDetalle';
import { PAGINACION_CANTIDAD_DEVOLVER, TABLA_TIPO_CLIENTE } from './../constantes';
import { ClienteService } from './cliente.service';
import { Cliente } from './cliente';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { LazyLoadEvent, SelectItem } from 'primeng/primeng';
import { OnInit, Component, EventEmitter, Output, ViewChild } from '@angular/core';
import { BaseComponent } from './../../comun/basecomponent';
import { DataTable } from 'primeng/components/datatable/datatable';

@Component({
    selector: 'cliente-selector',
    templateUrl: './cliente-selector.component.html'
})
export class ClienteSelectorComponent extends SelectorComponent {

    @ViewChild(DataTable) dataTableComponent: DataTable;

    @Output() block = new EventEmitter();
    @Output() unBlock = new EventEmitter();
    @Output() cargarSeleccionEvent = new EventEmitter();

    lstTipo: SelectItem[] = [];

    paginacion: ParametroPaginacion = new ParametroPaginacion();
    filtro: Cliente = new Cliente();
    seleccionado: Cliente;

    verSelector: boolean = false;

    constructor(private clienteService: ClienteService, private miscelaneoService: MiscelaneoService) {
        super();
    }

    ngOnInit() {

    }
    iniciarComponente() {
        this.paginacion = new ParametroPaginacion();
        this.dataTableComponent.reset();
        this.filtro = new Cliente();
        this.filtro.estado = 'A';
        this.cargarTipos();
    }

    cargarTipos() {
        this.block.emit();
        var filtroTipoReserva = new MiscelaneoDetalle();
        filtroTipoReserva.codigoTabla = TABLA_TIPO_CLIENTE;
        filtroTipoReserva.estado = 'A';
        this.lstTipo = [];
        this.seleccionado = null;
        this.lstTipo.push({ label: ' -- Todos -- ', value: null });
        this.miscelaneoService.listado(filtroTipoReserva).then(
            res => {
                res.forEach(reg => this.lstTipo.push({ label: reg.descripcion, value: reg.codigoElemento }));
                this.filtro.tipo = null;
                this.listarDefecto();
            }
        );

    }


    listarDefecto() {
        this.paginacion.registroInicio = 0;
        this.paginacion.cantidadRegistrosDevolver = this.registrosDevolver;
        this.clienteService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                console.log(this.paginacion);
                this.paginacion = pg;
                console.log(this.paginacion);
                this.unBlock.emit();
                this.verSelector = true;
            });
    }
    salir() {
        this.verSelector = false;
    }

    onRowSelect(event: any) {
        this.cargarSeleccionEvent.emit(event.data);
    }

    buscar() {
        this.dataTableComponent.reset();
    }
    cargarClientes(event: LazyLoadEvent) {
        if (!this.verSelector) {
            return;
        }
        this.block.emit();
        this.paginacion.listaResultado = [];
        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = event.rows;
        this.clienteService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                this.paginacion = pg;
                this.unBlock.emit();
            });
    }


}



