import { DtoTrasladoConductor } from './dto-traslado-conductor';
import { DtoTrasladoDia } from './dto-traslado-dia';
import { TrasladoDetalle } from './trasladodetalle';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Traslado } from './traslado';
import { Http, Headers, URLSearchParams } from '@angular/http';
import { Injectable } from '@angular/core';
import { REST_URL } from '../constantes';

@Injectable()
export class TrasladoService {

    private url = REST_URL + '/api/traslados/';
    private headers = new Headers({ 'Content-Type': 'application/json' });

    constructor(private http: Http) { }

    listarConPaginacion(filtro: Traslado, paginacion: ParametroPaginacion): Promise<ParametroPaginacion> {

        console.log(filtro);
        filtro.fecha = this.toDate(filtro.fecha);
        console.log(filtro);

        var params = new URLSearchParams();
        params.set("paginacion", JSON.stringify(paginacion));
        params.set("filtro", JSON.stringify(filtro));

        return this.http.get(`${this.url}listarconpaginacion`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.paginacion as ParametroPaginacion;
            }
            );
    }
    obtenerDetalle(nroTraslado: number): Promise<TrasladoDetalle[]> {
        var params = new URLSearchParams();
        params.set("nroTraslado", JSON.stringify(nroTraslado));

        return this.http.get(`${this.url}obtenerdetalle`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data as TrasladoDetalle[];
            }
            );
    }

    listarDiarios(): Promise<DtoTrasladoDia[]> {
        return this.http.get(`${this.url}listardiarios`)
            .toPromise().then(response => {
                var p = response.json();
                return p.data as DtoTrasladoDia[];
            }
            );
    }

    listarPorConductor(conductor: number): Promise<DtoTrasladoConductor[]> {
        var params = new URLSearchParams();
        params.set("conductor", JSON.stringify(conductor));

        return this.http.get(`${this.url}listarporconductor`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                var rawData = p.data as DtoTrasladoConductor[];
                return this.unirFechaConHora(rawData);
            }
            );
    }

    unirFechaConHora(rawData: DtoTrasladoConductor[]): DtoTrasladoConductor[] {

        if (rawData == null) {
            return [];
        }

        var data: DtoTrasladoConductor[] = [];

        rawData.forEach(reg => {

            reg.fecha = new Date(reg.fecha);

            var tras = new DtoTrasladoConductor();
            tras.title = reg.title;
            tras.destino = reg.destino;
            tras.estado = reg.estado;
            tras.fecha = reg.fecha;
            tras.placa = reg.placa;
            tras.ruta = reg.ruta;
            tras.nroDetalleTraslado = reg.nroDetalleTraslado;

            tras.start = new Date();
            tras.start.setFullYear(reg.fecha.getFullYear());
            tras.start.setMonth(reg.fecha.getMonth());
            tras.start.setDate(reg.fecha.getDate());

            tras.start.setHours(Number(reg.startS.substring(0, 2)));
            tras.start.setMinutes(Number(reg.startS.substring(3, 5)));
            tras.start.setSeconds(Number(reg.startS.substring(6, 8)));

            tras.end = new Date();
            tras.end.setFullYear(reg.fecha.getFullYear());
            tras.end.setMonth(reg.fecha.getMonth());
            tras.end.setDate(reg.fecha.getDate());

            tras.end.setHours(Number(reg.endS.substring(0, 2)));
            tras.end.setMinutes(Number(reg.endS.substring(3, 5)));
            tras.end.setSeconds(Number(reg.endS.substring(6, 8)));

            data.push(tras);

        });


        return data;
    }

    ingresarDetalle(detalle: TrasladoDetalle): Promise<string> {

        return this.http.post(this.url + 'ingresardetalle', detalle).toPromise().then(
            response => {
                return response.json().mensaje as string;
            }
        );
    }


    toDate(date: any): any {

        console.log(date);

        if (date == null || date == undefined) {
            return null;
        }
        else {
            var f = new Date(date);
            return f.getFullYear() + '-' + (f.getMonth() + 1) + "-" + f.getDate();
        }
    }

    actualizarEstado(id: number, estado: string): Promise<string> {

        var params = new URLSearchParams();
        params.set("id", JSON.stringify(id));
        params.set("estado", estado);

        return this.http.get(`${this.url}actualizarestado`, { search: params })
            .toPromise().then(response => {
                return 'Se han guardado los cambios';
            }
            );

    }
}



