import { DataTable } from 'primeng/components/datatable/datatable';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { RecursoTrasladoComponent } from './../asignacion/recurso-traslado.component';
import { TrasladoService } from './traslado.service';
import { PlanService } from './../plan/plan.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Traslado } from './traslado';
import { Plan } from './../plan/plan';
import { routes } from './../../shared/shell/app-routes/app.routes';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Component, OnInit, ViewChild } from '@angular/core';
import { SelectItem, LazyLoadEvent } from 'primeng/primeng';
import { BaseComponent } from '../../comun/basecomponent';

@Component({
    templateUrl: './traslado-listado.component.html'
})
export class TrasladoListadoComponent extends BaseComponent implements OnInit {

    @ViewChild(RecursoTrasladoComponent) recursoTrasladoComponent: RecursoTrasladoComponent;
    @ViewChild(DataTable) dataTableComponent: DataTable;

    filtro: Traslado = new Traslado();
    paginacion: ParametroPaginacion = new ParametroPaginacion();
    lstPlanes: SelectItem[] = [];

    constructor(private trasladoService: TrasladoService, private router: Router, private activatedRoute: ActivatedRoute, private planService: PlanService) {
        super();
    }

    ngOnInit() {
        this.lstPlanes = [];
        this.lstPlanes.push({ label: ' -- Todos -- ', value: null });
        this.filtro.nroPlan = null;
        this.filtro.auxReserva = +this.activatedRoute.snapshot.params['idReserva'];
        var filtro = new Plan();
        filtro.idReserva = +this.activatedRoute.snapshot.params['idReserva'];
        this.planService.listarPorReserva(filtro).then(
            res => {
                res.forEach(x => this.lstPlanes.push({ label: x.resumen, value: x.numero }));
            }
        );
    }

    listar() {
        this.dataTableComponent.reset();
    }

    cargarTraslados(event: LazyLoadEvent) {
        this.bloquearPagina();
        this.paginacion.registroInicio = event.first;
        this.paginacion.cantidadRegistrosDevolver = event.rows;
        this.paginacion.listaResultado = [];
        this.trasladoService.listarConPaginacion(this.filtro, this.paginacion)
            .then(pg => {
                this.paginacion = pg;
                this.desbloquearPagina();
            });
    }

    buscar(dt: any) {
        dt.reset();
    }

    salir() {
        this.router.navigate(['mavitours/reserva-listado']);
    }

    verRecursos(dto: Traslado) {
        this.recursoTrasladoComponent.traslado = dto;
        this.recursoTrasladoComponent.iniciarComponente();
    }

}



