import { MiscelaneoDetalle } from './miscelaneoDetalle';
import { Miscelaneo } from './miscelaneo';
import { Http, Headers, URLSearchParams } from '@angular/http';
import { Injectable } from '@angular/core';
import { REST_URL } from '../constantes';

@Injectable()
export class MiscelaneoService {

    private url = REST_URL + '/api/miscelaneos/';
    private headers = new Headers({ 'Content-Type': 'application/json' });

    constructor(private http: Http) { }

    listado(filtro: MiscelaneoDetalle): Promise<MiscelaneoDetalle[]> {
        let params = new URLSearchParams();
        params.set("filtro", JSON.stringify(filtro));
        return this.http.get(this.url + "listarporparametros", { search: params }).toPromise().then(
            response => {
                console.log();
                let data = response.json();
                return data.data as MiscelaneoDetalle[];
            }
        );
    }
    listarTablas(): Promise<Miscelaneo[]> {
        return this.http.get(this.url + "listartablas").toPromise().then(
            response => {
                let data = response.json();
                return data.data as Miscelaneo[];
            }
        );
    }
    ingresar(miscelaneo: MiscelaneoDetalle): Promise<string> {
        return this.http.post(this.url + "ingresar", miscelaneo, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo.message;
            }
        );
    }
    actualizar(miscelaneo: MiscelaneoDetalle): Promise<string> {
        return this.http.post(this.url + "actualizar", miscelaneo, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo.message;
            }
        );
    }
    obtenerPorId(bean: MiscelaneoDetalle): Promise<MiscelaneoDetalle> {

        let params = new URLSearchParams();
        params.set("bean", JSON.stringify(bean));

        return this.http.get(this.url + "obtenerporid", { search: params, headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo.data[0] as MiscelaneoDetalle;
            }
        );
    }
}



