import { FormGroup, FormControl } from '@angular/forms';
import { MiscelaneoDetalle } from './../mavitours/miscelaneo/miscelaneoDetalle';
import { PAGINACION_CANTIDAD_DEVOLVER } from './../mavitours/constantes';

import { ActivatedRoute, Router } from '@angular/router';
import { OnInit, Output, EventEmitter } from '@angular/core';
import { UError } from './uerror';
import { Message, SelectItem, ConfirmationService } from 'primeng/primeng';
import { UsuarioActual } from '../home/usuarioactual';
import { SeguridadService } from '../home/seguridad/seguridad.service';

export const enum accionSolicitada {
    NADA, NUEVO, EDITAR, VER, COPYINTERMEDIO, TRANSACCION
}
export const COMPANIA_GENERICA: string = '999999';
export const CODIGO_APP_LOGISTICA: string = 'WH';


export class BaseComponent {
    @Output() enviarMensaje = new EventEmitter();
    @Output() enviarListar = new EventEmitter();
    @Output() enviarMostrarDialog = new EventEmitter();
    lstEstadoMantenimiento: SelectItem[] = [];
    lstEstadoListado: SelectItem[] = [];
    registrosDevolver: number = PAGINACION_CANTIDAD_DEVOLVER;
    accion: accionSolicitada;
    msgs: Message[] = [];
    blocked: boolean = false;
    es: any;
    tb: any;

    constructor() { this.init(); }
    init() {
        this.es = {
            firstDayOfWeek: 1,
            dayNames: ["domingo", "lunes", "martes", "mircoles", "jueves", "viernes", "sbado"],
            dayNamesShort: ["dom", "lun", "mar", "mi", "jue", "vie", "sb"],
            dayNamesMin: ["D", "L", "M", "X", "J", "V", "S"],
            monthNames: ["enero", "febrero", "marzo", "abril", "mayo", "junio", "julio", "agosto", "septiembre", "octubre", "noviembre", "diciembre"],
            monthNamesShort: ["ene", "feb", "mar", "abr", "may", "jun", "jul", "ago", "sep", "oct", "nov", "dic"]
        }
        this.lstEstadoMantenimiento = [];
        this.lstEstadoMantenimiento.push({ label: ' -- Seleccionar -- ', value: null });
        this.lstEstadoMantenimiento.push({ label: 'Activo', value: 'A' });
        this.lstEstadoMantenimiento.push({ label: 'Inactivo', value: 'I' });

        this.lstEstadoListado = [];
        this.lstEstadoListado.push({ label: ' -- Todos -- ', value: null });
        this.lstEstadoListado.push({ label: 'Activo', value: 'A' });
        this.lstEstadoListado.push({ label: 'Inactivo', value: 'I' });

        //window.onbeforeunload = this.confirmOnPageExit;
    }

    confirmarRecargarPagina() {
        window.onbeforeunload = this.confirmOnPageExit;
    }

    bloquearPagina(): void {
        if (!this.blocked)
            this.blocked = true;
    }

    desbloquearPagina(): void {
        if (this.blocked)
            this.blocked = false;
    }

    flagABoolean(flag: string): boolean {
        if (flag == 'S')
            return true;
        else return false;
    }

    booleanAFlag(bool: boolean): string {
        if (bool)
            return 'S';
        else return 'N';
    }

    padDigits(numero: number, cantidadCeros: number): string {
        return Array(Math.max(cantidadCeros - String(numero).length + 1, 0)).join('0') + numero;
    }

    getAccionNombre() {
        if (this.accion) {
            switch (this.accion) {
                case accionSolicitada.EDITAR: return 'Editar';
                case accionSolicitada.VER: return 'Ver';
                case accionSolicitada.NUEVO: return 'Nuevo';
                case accionSolicitada.COPYINTERMEDIO: return 'Copy';
            }
        }
        return '';
    }

    confirmOnPageExit = function (e: Event) {
        e = e || window.event;
        var message = 'Deseas volver a cargar este sitio?';
        if (e)
            e.returnValue = message ? true : false;
        return message;
    };

    get ACCIONES() {
        return {
            NUEVO: accionSolicitada.NUEVO,
            EDITAR: accionSolicitada.EDITAR,
            VER: accionSolicitada.VER
        };
    }
    copiar<T>(instance: T): T {
        const copy = new (instance.constructor as { new(): T })();
        Object.assign(copy, instance);
        return copy;
    }
    estaVacio(cadena: string): boolean {
        if (cadena == null) {
            return true;
        }
        if (cadena == undefined) {
            return true;
        }
        if (cadena.trim() == '') {
            return true;
        }
    }
    obtenerFiltroMiscelaneo(tabla: number): MiscelaneoDetalle {
        var m = new MiscelaneoDetalle();
        m.estado = 'A';
        m.codigoTabla = tabla;
        return m;
    }

    mostrarMensaje(mensaje: Message) {
        this.msgs = [];
        this.msgs.push(mensaje);
    }

    mostrarMensajes(mensajes: Message[]) {
        this.msgs = [];
        mensajes.forEach(m => this.msgs.push(m));
    }

    getMensajeRegistrado(msg: string): Message {
        var mensaje: Message = new Object();
        mensaje.severity = 'info';
        mensaje.summary = 'Informacin';
        mensaje.detail = msg;
        return mensaje;
    }

    getMensajesError(msgs: string[]) {
        var stack: Message[] = [];
        msgs.forEach(
            er => {
                stack.push(this.getMensajeError(er));
            }
        );
        return stack;
    }

    getMensajeError(msg: string) {
        var mensaje: Message = new Object();
        mensaje.severity = 'error';
        mensaje.summary = 'Error';
        mensaje.detail = msg;
        return mensaje;
    }

    listar() {
        console.log('listar basecomponenet');
        this.buscar(this.tb);
    }

    buscar(tb: any) {
        tb.reset();
    }

    validateAllFormFields(formGroup: FormGroup) {         //{1}
        Object.keys(formGroup.controls).forEach(field => {  //{2}
            const control = formGroup.get(field);             //{3}
            if (control instanceof FormControl) {             //{4}
                control.markAsTouched({ onlySelf: true });
            } else if (control instanceof FormGroup) {        //{5}
                this.validateAllFormFields(control);            //{6}
            }
        });
    }

    resetAllFormFields(formGroup: FormGroup) {         //{1}
        Object.keys(formGroup.controls).forEach(field => {  //{2}
            const control = formGroup.get(field);             //{3}
            if (control instanceof FormControl) {
                control.markAsPristine();
                control.markAsUntouched();
            } else if (control instanceof FormGroup) {        //{5}
                this.validateAllFormFields(control);            //{6}
            }
        });
    }

    mostrarDialogInformacion(mensaje: string, serv: ConfirmationService) {
        serv.confirm({
            header: 'Informacin',
            message: mensaje
        });
    }
    getUsuarioActual(): UsuarioActual {
        return JSON.parse(sessionStorage.getItem('ua'));
    }

}




