import { state } from '@angular/animations';
import { Usuario } from './../../home/seguridad/usuario';
import { Traslado } from './../traslado/traslado';
import { Empleado } from './empleado';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Cliente } from './../cliente/cliente';
import { Http, Headers, URLSearchParams } from '@angular/http';
import { Injectable } from '@angular/core';
import { REST_URL } from '../constantes';

@Injectable()
export class EmpleadoService {

    private url = REST_URL + '/api/empleados/';
    private headers = new Headers({ 'Content-Type': 'application/json' });

    constructor(private http: Http) { }

    listarConPaginacion(filtro: Empleado, paginacion: ParametroPaginacion): Promise<ParametroPaginacion> {

        var params = new URLSearchParams();
        params.set("paginacion", JSON.stringify(paginacion));
        params.set("filtro", JSON.stringify(filtro));

        return this.http.get(`${this.url}listarconpaginacion`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.paginacion as ParametroPaginacion;
            }
            );
    }
    ingresar(empleado: Empleado): Promise<Usuario> {

        return this.http.post(`${this.url}ingresar`, empleado, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                var usua = respo.usuario as Usuario;
                usua.status = respo.status;
                return usua;
            }
        ).catch(
            res => {
                var respo = res.json();
                var usua = respo.usuario as Usuario;
                usua.status = respo.status;
                return usua;
            }
            );

    }

    obtenerPorId(id: number): Promise<Empleado> {
        var params = new URLSearchParams();
        params.set("id", JSON.stringify(id));

        return this.http.get(`${this.url}obtenerporid`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data[0] as Empleado;
            }
            );
    }

    actualizar(bean: Empleado): Promise<any> {
        return this.http.post(`${this.url}actualizar`, bean, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo;
            }
        ).catch(
            res => {
                var respo = res.json();
                return respo;
            }
            );
    }
}



