import { Usuario } from './../../home/seguridad/usuario';
import { Traslado } from './../traslado/traslado';
import { Conductor } from './conductor';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Cliente } from './../cliente/cliente';
import { Http, Headers, URLSearchParams } from '@angular/http';
import { Injectable } from '@angular/core';
import { REST_URL } from '../constantes';

@Injectable()
export class ConductorService {

    private url = REST_URL + '/api/conductores/';
    private headers = new Headers({ 'Content-Type': 'application/json' });

    constructor(private http: Http) { }

    listarConPaginacion(filtro: Conductor, paginacion: ParametroPaginacion): Promise<ParametroPaginacion> {

        var params = new URLSearchParams();
        params.set("paginacion", JSON.stringify(paginacion));
        params.set("filtro", JSON.stringify(filtro));

        return this.http.get(`${this.url}listarconpaginacion`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.paginacion as ParametroPaginacion;
            }
            );
    }
    listarDisponibles(filtro: Conductor, traslado: Traslado): Promise<Conductor[]> {
        var params = new URLSearchParams();
        console.log(traslado);
        params.set("traslado", JSON.stringify(traslado));
        params.set("filtro", JSON.stringify(filtro));

        return this.http.get(`${this.url}listardisponibles`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data as Conductor[];
            }
            );
    }
    ingresar(conductor: Conductor): Promise<Usuario> {

        return this.http.post(`${this.url}ingresar`, conductor, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                var usua = respo.usuario as Usuario;
                usua.status = respo.status;
                return usua;
            }
        ).catch(
            res => {
                var respo = res.json();
                var usua = respo.usuario as Usuario;
                usua.status = respo.status;
                return usua;
            }
            );

    }


    obtenerPorId(id: number): Promise<Conductor> {
        var params = new URLSearchParams();
        params.set("id", JSON.stringify(id));

        return this.http.get(`${this.url}obtenerporid`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data[0] as Conductor;
            }
            );
    }

    actualizar(bean: Conductor): Promise<any> {
        return this.http.post(`${this.url}actualizar`, bean, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo;
            }
        ).catch(
            res => {
                var respo = res.json();
                return respo;
            }
            );
    }
}



