import { Plan } from './plan';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Http, Headers, URLSearchParams } from '@angular/http';
import { Injectable } from '@angular/core';
import { REST_URL } from '../constantes';

@Injectable()
export class PlanService {

    private url = REST_URL + '/api/planes/';
    private headers = new Headers({ 'Content-Type': 'application/json' });

    constructor(private http: Http) { }

    listarPorReserva(filtro: Plan): Promise<Plan[]> {

        var params = new URLSearchParams();
        params.set("filtro", JSON.stringify(filtro));

        return this.http.get(`${this.url}listarporreserva`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data as Plan[];
            }
            );
    }

    anular(bean: Plan) {

        return this.http.post(`${this.url}anular`, bean)
            .toPromise().then(response => {
                var p = response.json();
                return p.res as string;
            });
    }

}



