import { ProveedorSalud } from './proveedorsalud';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Http, Headers, URLSearchParams } from '@angular/http';
import { Injectable } from '@angular/core';
import { DtoProveedores, DtoProveedoresData } from './dtoproveedores';
import { REST_URL } from '../constantes';

@Injectable()
export class ProveedorSaludService {

    private url = REST_URL + '/api/proveedores/';
    private headers = new Headers({ 'Content-Type': 'application/json' });

    constructor(private http: Http) {

    }

    listarConPaginacion(filtro: ProveedorSalud, paginacion: ParametroPaginacion): Promise<ParametroPaginacion> {

        var params = new URLSearchParams();
        params.set("paginacion", JSON.stringify(paginacion));
        params.set("filtro", JSON.stringify(filtro));

        return this.http.get(`${this.url}listarconpaginacion`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.paginacion as ParametroPaginacion;
            }
            );
    }

    listarTodos(paginacion: ParametroPaginacion): Promise<ParametroPaginacion> {

        var filtro = new DtoProveedoresData();
        filtro.descripcion = "";
        filtro.id = null;
        filtro.telefono = "";
        filtro.correo = "";

        var params = new URLSearchParams();
        params.set("paginacion", JSON.stringify(paginacion));
        params.set("filtro", JSON.stringify(filtro));


        return this.http.get(`${this.url}listarConPaginacion`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.paginacion as ParametroPaginacion;
            }
            );
    }
    listarActivos(): Promise<ProveedorSalud[]> {

        return this.http.get(`${this.url}listaractivos`)
            .toPromise().then(response => {
                var p = response.json();
                return p.data as ProveedorSalud[];
            }
            );
    }

    ingresar(proveedorSalud: ProveedorSalud): Promise<string> {

        return this.http.post(`${this.url}ingresar`, proveedorSalud, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo.message;
            }
        ).catch(
            res => {
                var respo = res.json();
                return respo.message;
            }
            );

    }

    obtenerPorId(id: number): Promise<ProveedorSalud> {
        var params = new URLSearchParams();
        params.set("id", JSON.stringify(id));

        return this.http.get(`${this.url}obtenerporid`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data[0] as ProveedorSalud;
            }
            );
    }

    actualizar(bean: ProveedorSalud): Promise<any> {
        return this.http.post(`${this.url}actualizar`, bean, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo.message;
            }
        ).catch(
            res => {
                var respo = res.json();
                return respo;
            }
            );
    }

}



