import { DomSanitizer } from '@angular/platform-browser';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
    name: 'dias'
})

export class DiasPipe implements PipeTransform {
    constructor() { }
    transform(value: any) {

        console.log(value);

        var dias: string[] = value.split(',');
        var res: string = '';

        dias.forEach(d => {

            switch (d) {
                case '2': res += 'L, '; break;
                case '3': res += 'M, '; break;
                case '4': res += 'X, '; break;
                case '5': res += 'J, '; break;
                case '6': res += 'V, '; break;
                case '7': res += 'S, '; break;
                case '1': res += 'D, '; break;
                default: '';
            }

        });

        return res.substring(0, res.length - 2);
    }
}


