import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { VehiculoService } from './vehiculo-service';
import { ProveedorSaludService } from './../proveedordeseguro/proveedorsalud.service';
import { TABLA_TIPO_CATEGORIA_VEHICULO, TABLA_TIPO_EMPRESA, TABLA_TIPO_MARCA_VEHICULO, REGEXP_PLACA, REGEXP_NOT_EMPTY } from './../constantes';
import { MiscelaneoDetalle } from './../miscelaneo/miscelaneoDetalle';
import { MiscelaneoService } from './../miscelaneo/miscelaneo.service';
import { SelectItem, Message } from 'primeng/primeng';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { OnInit, Component } from '@angular/core';
import { BaseComponent } from './../../comun/basecomponent';
import { Vehiculo } from './vehiculo';

@Component({
    selector: 'vehiculo-mantenimiento',
    templateUrl: './vehiculo-mantenimiento.component.html'
})
export class VechiculoMantenimientoComponent extends BaseComponent implements OnInit {

    accionNombre: string = "";
    verModal: boolean = false;
    lstCategoria: SelectItem[] = [];
    lstEmpresa: SelectItem[] = [];
    lstMarca: SelectItem[] = [];
    lstProveedor: SelectItem[] = [];
    lstEstado: SelectItem[] = [];
    vehiculo: Vehiculo = new Vehiculo();

    vehiculoForm: FormGroup;

    constructor(private miscelaneoService: MiscelaneoService, private proveedorSaludService: ProveedorSaludService, private vehiculoService: VehiculoService, private translateService: TranslateService, private fb: FormBuilder) {
        super();
    }

    ngOnInit() {
        this.iniciarFormValidator();
    }

    iniciarFormValidator() {
        this.vehiculoForm = this.fb.group({
            'id': new FormControl(null),
            'placa': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_PLACA.source)]),
            'denominacion': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_NOT_EMPTY.source)]),
            'categoria': new FormControl(null, [Validators.required]),
            'chasis': new FormControl(null),
            'empresa': new FormControl(null, [Validators.required]),
            'motor': new FormControl(null),
            'marca': new FormControl(null, [Validators.required]),
            'modelo': new FormControl(null),
            'estado': new FormControl(null, [Validators.required]),
            'capacidad': new FormControl(null, [Validators.required, Validators.min(1), Validators.max(200)]),
            'anio': new FormControl(null),

            'provSOAT': new FormControl(null, [Validators.required]),
            'polizaSOAT': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_NOT_EMPTY.source)]),
            'inicioSOAT': new FormControl(null, [Validators.required]),
            'finSOAT': new FormControl(null, [Validators.required]),

            'provEIRL': new FormControl(null, [Validators.required]),
            'polizaEIRL': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_NOT_EMPTY.source)]),
            'inicioEIRL': new FormControl(null, [Validators.required]),
            'finEIRL': new FormControl(null, [Validators.required]),

        });
    }

    iniciarComponente() {
        this.accion = this.ACCIONES.NUEVO;
        this.accionNombre = this.getAccionNombre();
        this.verModal = true;
    }

    salir() {
        this.verModal = false;
    }

    guardar() {

        this.bloquearPagina();

        this.validateAllFormFields(this.vehiculoForm);

        if (!this.vehiculoForm.valid) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error', detail: 'Ingresar correctamente los datos' });
            this.validarExtra();
            this.desbloquearPagina();
            return;
        }

        if (this.accion == this.ACCIONES.NUEVO) {
            this.vehiculoService.ingresar(this.vehiculo).then(
                res => {
                    if (res.status != 0) {
                        this.enviarMensaje.emit([this.getMensajeRegistrado(res.message)]);
                        this.enviarListar.emit();
                        this.salir();
                        this.desbloquearPagina();
                    }
                    else {
                        var ms: string[] = res.message.split(';');
                        var trad: Message[] = [];
                        ms.forEach(mm => {
                            this.translateService.get(mm).subscribe(es => trad.push(this.getMensajeError(es)));
                        });
                        this.enviarMensaje.emit(trad);
                        this.desbloquearPagina();
                    }
                }
            );
        }
        else {
            this.vehiculoService.actualizar(this.vehiculo).then(
                res => {
                    if (res.status != 0) {
                        this.enviarMensaje.emit([this.getMensajeRegistrado(res.message)]);
                        this.enviarListar.emit();
                        this.salir();
                        this.desbloquearPagina();
                    }
                    else {
                        var ms: string[] = res.message.split(';');
                        var trad: Message[] = [];
                        ms.forEach(mm => {
                            this.translateService.get(mm).subscribe(es => trad.push(this.getMensajeError(es)));
                        });
                        this.enviarMensaje.emit(trad);
                        this.desbloquearPagina();
                    }
                }
            );
        }

    }

    editar(bean: Vehiculo) {
        this.resetAllFormFields(this.vehiculoForm);
        this.msgs = [];
        this.accion = this.ACCIONES.EDITAR;
        this.accionNombre = this.getAccionNombre();
        this.verModal = true;
        this.bloquearPagina();

        this.lstEstado = [];
        this.lstEstado.push({ label: " -- Seleccionar -- ", value: null });
        this.lstEstado.push({ label: 'Activo', value: 'A' });
        this.lstEstado.push({ label: 'Inactivo', value: 'I' });

        this.cargarCombos().then(
            res => {
                this.vehiculoService.obtenerPorId(bean.id).then(
                    re => {
                        this.vehiculo = re;

                        this.vehiculo.vigenciaFinEIRL = this.vehiculo.vigenciaFinEIRL == undefined ? null : this.vehiculo.vigenciaFinEIRL == null ? null : new Date(this.vehiculo.vigenciaFinEIRL);
                        this.vehiculo.vigenciaInicioEIRL = this.vehiculo.vigenciaInicioEIRL == undefined ? null : this.vehiculo.vigenciaInicioEIRL == null ? null : new Date(this.vehiculo.vigenciaInicioEIRL);
                        this.vehiculo.vigenciaInicioSOAT = this.vehiculo.vigenciaInicioSOAT == undefined ? null : this.vehiculo.vigenciaInicioSOAT == null ? null : new Date(this.vehiculo.vigenciaInicioSOAT);
                        this.vehiculo.vigenciaFinSOAT = this.vehiculo.vigenciaFinSOAT == undefined ? null : this.vehiculo.vigenciaFinSOAT == null ? null : new Date(this.vehiculo.vigenciaFinSOAT);
                        this.desbloquearPagina();
                    }
                );
            }
        );
    }


    validarExtra() {

        var fechasSoat = true;

        if (this.vehiculo.vigenciaInicioSOAT == null) {
            fechasSoat = false;
        }
        if (this.vehiculo.vigenciaFinSOAT == null) {
            fechasSoat = false;
        }

        if (fechasSoat && (this.vehiculo.vigenciaInicioSOAT >= this.vehiculo.vigenciaFinSOAT)) {
            this.msgs.push({ severity: 'error', summary: 'Error', detail: 'Las fechas de vigencia del SOAT deben ser coherentes' });
        }

        var fechasEirl = true;

        if (this.vehiculo.vigenciaInicioEIRL == null) {
            fechasEirl = false;
        }
        if (this.vehiculo.vigenciaFinEIRL == null) {
            fechasEirl = false;
        }
        if (fechasEirl && (this.vehiculo.vigenciaInicioEIRL >= this.vehiculo.vigenciaFinEIRL)) {
            this.msgs.push({ severity: 'error', summary: 'Error', detail: 'Las fechas de vigencia del EIRL deben ser coherentes' });
        }

    }

    nuevo() {

        this.resetAllFormFields(this.vehiculoForm);
        this.msgs = [];
        this.accion = this.ACCIONES.NUEVO;
        this.accionNombre = this.getAccionNombre();
        this.vehiculo = new Vehiculo();
        this.vehiculo.estado = 'A';
        this.vehiculo.nroAsientos = 1;
        this.accionNombre = this.getAccionNombre();
        this.verModal = true;
        this.bloquearPagina();
        this.cargarCombos().then(
            res => {
                this.lstEstado = [];
                this.lstEstado.push({ label: " -- Seleccionar -- ", value: null });
                this.lstEstado.push({ label: 'Activo', value: 'A' });
                this.lstEstado.push({ label: 'Inactivo', value: 'I' });
                this.desbloquearPagina();
            }
        );
    }

    cargarCombos(): Promise<number> {


        var p1 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_CATEGORIA_VEHICULO)).then(
            r1 => {
                this.lstCategoria = [];
                this.lstCategoria.push({ label: ' -- Seleccionar -- ', value: null });
                r1.forEach(rr1 => this.lstCategoria.push({ label: rr1.descripcion, value: rr1.codigoElemento }));
            }
        );

        var p2 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_EMPRESA)).then(
            r2 => {
                this.lstEmpresa = [];
                this.lstEmpresa.push({ label: ' -- Seleccionar -- ', value: null });
                r2.forEach(rr2 => this.lstEmpresa.push({ label: rr2.descripcion, value: rr2.codigoElemento }));
            }
        );

        var p3 = this.miscelaneoService.listado(this.obtenerFiltroMiscelaneo(TABLA_TIPO_MARCA_VEHICULO)).then(
            r3 => {
                this.lstMarca = [];
                this.lstMarca.push({ label: ' -- Seleccionar -- ', value: null });
                r3.forEach(rr3 => this.lstMarca.push({ label: rr3.descripcion, value: rr3.codigoElemento }));
            }
        );

        var p4 = this.proveedorSaludService.listarActivos().then(
            r4 => {
                this.lstProveedor = [];
                this.lstProveedor.push({ label: ' -- Seleccionar -- ', value: null });
                r4.forEach(rr4 => this.lstProveedor.push({ label: rr4.descripcion, value: rr4.id }));
            }
        );

        return Promise.all([p1, p2, p3, p4]).then(
            res => {
                return 1
            }
        );
    }

}



