import { UError } from './../../comun/uerror';
import { Rol } from './rol';
import { Opcion } from './opcion';
import { UsuarioActual } from './../usuarioactual';
import { JWT_RESPONSE_HEADER, TIME } from './constantes';
import { Http, Headers, Response, URLSearchParams } from '@angular/http';
import { Injectable } from '@angular/core';
import { REST_URL } from '../../mavitours/constantes';

@Injectable()
export class SeguridadService {

    private url = REST_URL + '/api/seguridad/';

    constructor(private http: Http) { }

    validarUsuarioClave(usuario: string, clave: string): Promise<any> {
        var u: UsuarioActual = new UsuarioActual();
        u.usuario = 'ADMIN';

        var params = new URLSearchParams();
        params.set('usuario', usuario);
        params.set('clave', clave);
        return this.http.get(this.url + 'validarcredenciales', { search: params }).toPromise().then(res => {
            return res.json().data[0] as UsuarioActual;
        }).catch(err => {
            console.log("catch");
            var error = new UError();
            error.messages.push(err.json().message);
            console.log(error);
            return error;
        });
    }

    isLoggedIn() {
        return JSON.parse(sessionStorage.getItem('ua'));
        //return !!sessionStorage.getItem(JWT_RESPONSE_HEADER);
    }

    listarOpcionesPorUsuario(usuario: string): Promise<Opcion[]> {
        var params = new URLSearchParams();
        params.set('usuario', usuario);

        return this.http.get(`${this.url}listaropcionesporusuario`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data as Opcion[];
            }
            );
    }

    private saveToken(res: Response): any {
        sessionStorage.setItem(JWT_RESPONSE_HEADER, "temp");
        return res;
    }

    obtenerOpcionPorUsuario(usuario: string, ruta: string): Promise<Opcion[]> {

        var params = new URLSearchParams();
        params.set('usuario', usuario);
        params.set('ruta', ruta);

        return this.http.get(`${this.url}obteneropcionporusuario`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data as Opcion[];
            }
            );
    }
    listarRolesActivos(): Promise<Rol[]> {
        return this.http.get(`${this.url}listarroles`)
            .toPromise().then(response => {
                var p = response.json();
                return p.data as Rol[];
            }
            );
    }

    cambiarClave(usuario: string, clave: string, clave1: string, clave2: string): Promise<Response> {

        let params = new URLSearchParams();
        params.set('usuario', usuario);
        params.set('clave', clave);
        params.set('clave1', clave1);
        params.set('clave2', clave2);
        return this.http.get(`${this.url}cambiarClave`, { search: params })
            .toPromise()
            .then(r => {
                return r.json() as Response;
            });
    }
}



