import { DtoTrasladoDia } from './../../mavitours/traslado/dto-traslado-dia';
import { TrasladoService } from './../../mavitours/traslado/traslado.service';
import { VehiculoService } from './../../mavitours/vehiculo/vehiculo-service';
import { DtoVehiculoVencimiento } from './../../mavitours/vehiculo/dto-vehiculo-vencimiento';
import { SeguridadService } from './../seguridad/seguridad.service';
import { BaseComponent } from './../../comun/basecomponent';
import { Component, OnInit } from '@angular/core';
import { UsuarioActual } from '../usuarioactual';
import { DtoTrasladoConductor } from '../../mavitours/traslado/dto-traslado-conductor';
import { SelectItem, ConfirmationService } from 'primeng/primeng';

@Component({
    moduleId: module.id,
    selector: 'bienvenida',
    templateUrl: 'bienvenida.component.html'
})
export class BienvenidaComponent extends BaseComponent implements OnInit {

    traslados: any[] = [];
    soatVencer: DtoVehiculoVencimiento[] = [];
    eirlVencer: DtoVehiculoVencimiento[] = [];
    diarios: DtoTrasladoDia[] = []
    headerConfig: any;
    esConductor: boolean = true;

    constructor(private vehiculoService: VehiculoService, private trasladoService: TrasladoService, private confirmationService: ConfirmationService) {
        super();
        this.esConductor = true;
    }

    ngOnInit() {

        this.bloquearPagina();

        this.esConductor = this.getUsuarioActual().rol == 2;

        if (this.esConductor) {
            this.cargarConductor();
        }
        else {
            this.cargarNormal();
        }
    }

    buscar() {
    }

    cargarNormal() {

        var p1 = this.vehiculoService.listarPorVencer('S').then(
            res => {
                this.soatVencer = res;
            }
        );

        var p2 = this.vehiculoService.listarPorVencer('E').then(
            res => {
                this.eirlVencer = res;
            }
        );

        var p3 = this.trasladoService.listarDiarios().then(
            res => {
                this.diarios = res;
            }
        );

        Promise.all([p1, p2, p3]).then(
            r => {
                this.desbloquearPagina();
            }
        );

    }

    cargarConductor() {
        this.headerConfig = {
            left: 'prev,next today',
            center: 'title',
            right: 'month,agendaWeek,agendaDay'
        };


        this.trasladoService.listarPorConductor(this.getUsuarioActual().id).then(
            res => {
                this.traslados = res;
                this.desbloquearPagina();
            }
        );

        this.lstEstadoTraslado = [];
        this.lstEstadoTraslado.push({ label: 'Anulado', value: 'X' });
        this.lstEstadoTraslado.push({ label: 'Ejecutado', value: 'E' });
        this.lstEstadoTraslado.push({ label: 'Pendiente', value: 'P' });

    }

    lstEstadoTraslado: SelectItem[] = [];
    verModaInformacion: boolean = false;
    regSeleccionado: DtoTrasladoConductor = new DtoTrasladoConductor();

    verInformacion(event: any) {
        this.regSeleccionado = event.calEvent;

        var i = new Date(this.regSeleccionado.start);
        var e = new Date(this.regSeleccionado.end);

        this.regSeleccionado.horaInicio = i.toLocaleTimeString().substring(0, 5);
        this.regSeleccionado.horaFin = e.toLocaleTimeString().substring(0, 5);


        this.verModaInformacion = true;

    }

    guardarInfoTraslado() {

        if (this.regSeleccionado.estado == 'X') {
            this.anularTraslado();
            return null;
        }

        this.bloquearPagina();

        this.trasladoService.actualizarEstado(this.regSeleccionado.nroDetalleTraslado, this.regSeleccionado.estado).then(
            res => {
                this.msgs = [];
                this.msgs.push({ severity: 'success', summary: 'Informacin', detail: res });
                this.verModaInformacion = false;
                this.trasladoService.listarPorConductor(this.getUsuarioActual().id).then(
                    res => {
                        this.traslados = res;
                        this.desbloquearPagina();
                    }
                );


            }
        );

    }

    anularTraslado() {
        this.confirmationService.confirm({
            header: 'Confirmacin',
            message: 'Desea anular el registro?',
            accept: () => {
                this.bloquearPagina();

                this.trasladoService.actualizarEstado(this.regSeleccionado.nroDetalleTraslado, this.regSeleccionado.estado).then(
                    res => {
                        this.msgs = [];
                        this.msgs.push({ severity: 'success', summary: 'Informacin', detail: res });
                        this.verModaInformacion = false;
                        this.trasladoService.listarPorConductor(this.getUsuarioActual().id).then(
                            res => {
                                this.traslados = res;
                                this.desbloquearPagina();
                            }
                        );


                    }
                );
            }
        });

    }

}


