import { VehiculoService } from './../vehiculo/vehiculo-service';
import { ConductorService } from './../conductor/conductor.service';
import { Traslado } from './../traslado/traslado';
import { Vehiculo } from './../vehiculo/vehiculo';
import { Conductor } from './../conductor/conductor';
import { SelectorComponent } from './../../comun/selector.component';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { LazyLoadEvent } from 'primeng/primeng';
import { OnInit, Component, EventEmitter, Output } from '@angular/core';
import { BaseComponent } from './../../comun/basecomponent';

@Component({
    selector: 'recurso-selector',
    templateUrl: './recurso-selector.component.html'
})
export class RecursoSelectorComponent extends SelectorComponent {

    @Output() block = new EventEmitter();
    @Output() unBlock = new EventEmitter();
    @Output() cargarSeleccionEvent = new EventEmitter();

    lstConductores: Conductor[] = [];
    filtroConductor: Conductor = new Conductor();
    seleccionadoConductor: Conductor;

    lstVehiculos: Vehiculo[] = [];
    filtroVehiculo: Vehiculo = new Vehiculo();
    seleccionadoVehiculo: Vehiculo;

    traslado: Traslado = new Traslado();

    verSelector: boolean = false;

    constructor(private conductorService: ConductorService, private vehiculoService: VehiculoService) {
        super();
    }

    iniciarComponente() {
        this.filtroConductor = new Conductor();
        this.filtroVehiculo = new Vehiculo();
        this.seleccionadoConductor = null;
        this.seleccionadoVehiculo = null;
        this.listarDefecto();
    }

    listarDefecto() {

        this.traslado = this.convertir(this.traslado);

        this.buscarConductores();
        this.buscarVehiculos();
        this.verSelector = true;
    }
    salir() {
        this.verSelector = false;
    }

    buscarConductores() {
        this.conductorService.listarDisponibles(this.filtroConductor, this.traslado).then(
            res => {
                this.lstConductores = res;
            }
        );

    }

    aceptar() {

        if (this.seleccionadoConductor == null || this.seleccionadoVehiculo == null) {
            this.msgs = [];
            this.msgs.push(this.getMensajeError('Seleccionar ambos recursos'));
            return;
        }

        var todo = { conductor: this.seleccionadoConductor, vehiculo: this.seleccionadoVehiculo };
        this.cargarSeleccionEvent.emit(todo);
        this.verSelector = false;
    }

    buscarVehiculos() {

        this.vehiculoService.listarDisponibles(this.filtroVehiculo, this.traslado).then(
            res => {
                this.lstVehiculos = res;
            }
        );
    }
    convertir(tr: Traslado): Traslado {
        tr.fecha = new Date(tr.fecha);
        tr.horaPartida = new Date(tr.horaPartida);
        tr.horaDestino = new Date(tr.horaDestino);

        tr.fechaSinHora = tr.fecha.getFullYear() + "-" + (tr.fecha.getMonth() + 1) + "-" + tr.fecha.getDate();
        tr.horaPartidaMinutos = tr.horaPartida.getHours() * 60 + tr.horaPartida.getMinutes();
        tr.horaDestinoMinutos = tr.horaDestino.getHours() * 60 + tr.horaDestino.getMinutes();

        return tr;
    }

}



