import { SelectItem } from 'primeng/primeng';
import { SeguridadService } from './../../../home/seguridad/seguridad.service';
import { Router } from '@angular/router';
import { BaseComponent } from './../../../comun/basecomponent';
import { UsuarioActual } from './../../../home/usuarioactual';
import { Component, Inject, forwardRef, OnInit } from '@angular/core';
import { ShellComponent } from "../shell.component";
import { TranslateService } from '@ngx-translate/core';

@Component({
    selector: 'app-topbar',
    templateUrl: './app.topbar.component.html'
})
export class AppTopBar extends BaseComponent implements OnInit {

    usuario: UsuarioActual = new UsuarioActual();

    constructor(
        @Inject(forwardRef(() => ShellComponent))
        public app: ShellComponent,
        private router: Router,
        private translateService: TranslateService) {
        super();
        this.translateService.setDefaultLang("es");
    }

    ngOnInit() {
        this.usuario = this.getUsuarioActual();
    }

    cambiarLenguaje(len: string) {
        console.log(len);
        this.translateService.use(len);
    }

    logout() {
        sessionStorage.clear();
        this.router.navigate(['login']);
    }

}


