import { TrasladoDetalle } from './trasladodetalle';
export class Traslado {


    constructor() {
        this.lstRecursos = [];
    }

    nroTraslado: number;
    nroPlan: number;
    capacidad: number;
    tipo: string;
    fecha: Date;
    ptoPartida: string;
    horaPartida: Date;
    ptoDestino: string;
    horaDestino: Date;
    rutaEspecifica: string;
    estado: string;

    cliente: number;
    clienteNombre: string;
    tipoNombre: string;
    horaPartidaNombre: string;
    actual: number;
    auxReserva: number;
    nroReserva: number;

    lstRecursos: TrasladoDetalle[];

    horaPartidaMinutos: number;
    horaDestinoMinutos: number;
    fechaSinHora: string;
    libre: number;
}


