import { ContraseniaMantenientoComponent } from './../shared/shell/contrasenia/contrasenia-mantenimiento.component';
import { EmpleadoService } from './../mavitours/empleado/empleado.service';
import { EmpleadoMantenimientoComponent } from './../mavitours/empleado/empleado-mantenimiento.component';
import { EmpleadoListadoComponent } from './../mavitours/empleado/empleado-listado.component';
import { RecursoSelectorComponent } from './../mavitours/asignacion/recurso-selector.component';
import { Component404 } from './404.component';
import { DiaNombrePipe } from './../comun/pipes/diaNombre.pipe';
import { DiasPipe } from './../comun/pipes/dias.pipe';
import { PlanService } from './../mavitours/plan/plan.service';
import { TrasladoListadoComponent } from './../mavitours/traslado/traslado-listado.component';
import { RecursoTrasladoMantenimientoComponent } from './../mavitours/asignacion/recurso-traslado-mantenimiento.component';
import { RecursoTrasladoComponent } from './../mavitours/asignacion/recurso-traslado.component';
import { ClienteSelectorComponent } from './../mavitours/cliente/cliente-selector.component';
import { ProveedorSaludMantenimientoComponent } from './../mavitours/proveedordeseguro/proveedorsalud-mantenimiento.component';
import { VechiculoMantenimientoComponent } from './../mavitours/vehiculo/vehiculo-mantenimiento.component';
import { ConductorMantenimientoComponent } from './../mavitours/conductor/conductor-mantenimiento.component';
import { MiscelaneoMantenimientoComponent } from './../mavitours/miscelaneo/miscelaneo-mantenimiento.component';
import { ClienteMantenimientoComponent } from './../mavitours/cliente/cliente-mantenimiento.component';
import { VehiculoListadoComponent } from './../mavitours/vehiculo/vehiculo-listado.component';
import { VehiculoService } from './../mavitours/vehiculo/vehiculo-service';
import { ProveedorSaludService } from './../mavitours/proveedordeseguro/proveedorsalud.service';
import { ConductorListadoComponent } from './../mavitours/conductor/conductor-listado.component';
import { ProveedorSaludListadoComponent } from './../mavitours/proveedordeseguro/proveedorsalud-listado.component';
import { ConductorService } from './../mavitours/conductor/conductor.service';
import { MiscelaneoService } from './../mavitours/miscelaneo/miscelaneo.service';
import { TrasladoService } from './../mavitours/traslado/traslado.service';
import { ClienteService } from './../mavitours/cliente/cliente.service';
import { ReservaService } from './../mavitours/reserva/reserva.service';
import { AsignacionListadoComponent } from './../mavitours/asignacion/asignacion-listado.component';
import { ReservaListadoComponent } from './../mavitours/reserva/reserva-listado.component';
import { MiscelaneoListadoComponent } from './../mavitours/miscelaneo/miscelaneo-listado.component';
import { ClienteListadoComponent } from './../mavitours/cliente/cliente-listado.component';
import { SeguridadService } from './seguridad/seguridad.service';
import { AppFooterComponent } from './../shared/shell/app-footer/app-footer.component';
import { AppTopBar } from './../shared/shell/app-tool-bar/app.topbar.component';
import { AppSubMenu, AppMenuComponent } from './../shared/shell/app-menu/app-menu.component';
import { ShellComponent } from './../shared/shell/shell.component';
import { BienvenidaComponent } from './bienvenida/bienvenida.component';
import { ScheduleModule, TooltipModule, ConfirmDialog, ConfirmationService, BlockUIModule, CheckboxModule, SpinnerModule, GrowlModule, CalendarModule, PanelModule, DataTableModule, DialogModule, DropdownModule, InputTextModule, ButtonModule, ConfirmDialogModule } from 'primeng/primeng';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LoginComponent } from './login/login.component';
import { FormBuilder, FormsModule, ReactiveFormsModule } from '@angular/forms';
import { GalleriaModule } from 'primeng/primeng';

import { Routes, RouterModule } from '@angular/router/'
import { HttpClientModule, HttpClient } from '@angular/common/http';
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { HttpLoaderFactory } from '../app.module';
import { SeguridadActivator } from './seguridad/seguridad.activator';
import { ReservaMantenimientoComponent } from '../mavitours/reserva/reserva-mantenimiento.component';
import { VehiculoSelectorComponent } from '../mavitours/vehiculo/vehiculo-selector.component';
import { ConductorSelectorComponent } from '../mavitours/conductor/conductor-selector.component';
import { SafePipe } from '../comun/pipes/safe.pipe';
import { ReportePdfComponent } from '../comun/reporte/reportepdf.component';
const routes: Routes = [
  {
    path: '',
    component: LoginComponent
  },
  {
    path: '404',
    component: Component404
  },
  {
    path: 'login',
    component: LoginComponent
  },
  {
    path: 'mavitours',
    component: ShellComponent,
    canActivate: [SeguridadActivator],
    children: [
      {
        path: 'bienvenida',
        component: BienvenidaComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'proveedor-listado',
        component: ProveedorSaludListadoComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'conductor-listado',
        component: ConductorListadoComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'empleado-listado',
        component: EmpleadoListadoComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'cliente-listado',
        component: ClienteListadoComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'miscelaneo-listado',
        component: MiscelaneoListadoComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'reserva-listado',
        component: ReservaListadoComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'reserva-mantenimiento',
        component: ReservaMantenimientoComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'reserva-mantenimiento/:reserva',
        component: ReservaMantenimientoComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'asignacion-listado',
        component: AsignacionListadoComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'vehiculo-listado',
        component: VehiculoListadoComponent,
        canActivate: [SeguridadActivator]
      },
      {
        path: 'traslado-listado/:idReserva',
        component: TrasladoListadoComponent,
        canActivate: [SeguridadActivator]
      }

    ]
  }

]

@NgModule({
  imports: [
    ConfirmDialogModule, ScheduleModule,
    CommonModule, GalleriaModule, TooltipModule,
    RouterModule.forRoot(routes), CheckboxModule,
    RouterModule.forChild(routes), CalendarModule, PanelModule, DataTableModule, DialogModule,
    BlockUIModule, GrowlModule, FormsModule, ReactiveFormsModule, DropdownModule, InputTextModule,
    HttpClientModule, DataTableModule, FormsModule, ButtonModule, SpinnerModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [HttpClient]
      }
    })
  ],
  declarations: [ReportePdfComponent, SafePipe, EmpleadoListadoComponent, EmpleadoMantenimientoComponent, RecursoSelectorComponent, Component404, DiaNombrePipe, DiasPipe, TrasladoListadoComponent, RecursoTrasladoMantenimientoComponent, RecursoTrasladoComponent, LoginComponent, BienvenidaComponent, ProveedorSaludListadoComponent, ShellComponent, AppMenuComponent, AppSubMenu, AppTopBar, AppFooterComponent, ConductorListadoComponent, ClienteListadoComponent, MiscelaneoListadoComponent, ReservaListadoComponent, AsignacionListadoComponent, VehiculoListadoComponent, ClienteMantenimientoComponent, MiscelaneoMantenimientoComponent, ConductorMantenimientoComponent, VechiculoMantenimientoComponent, ProveedorSaludMantenimientoComponent, ReservaMantenimientoComponent, ClienteSelectorComponent, VehiculoSelectorComponent, ConductorSelectorComponent, ContraseniaMantenientoComponent],
  providers: [SeguridadActivator, EmpleadoService, PlanService, ConfirmationService, ProveedorSaludService, SeguridadService, ReservaService, ClienteService, TrasladoService, MiscelaneoService, ConductorService, VehiculoService],
  exports: [ShellComponent]

})
export class HomeModule { }



