import { TIME } from './../seguridad/constantes';
import { SeguridadService } from './../seguridad/seguridad.service';
import { BaseComponent } from './../../comun/basecomponent';
import { Router } from '@angular/router';
import { Message } from 'primeng/primeng';
import { Validators, FormControl, FormGroup, FormBuilder } from '@angular/forms';
import { Component, OnInit } from '@angular/core';
import 'rxjs/add/operator/toPromise';
import { TranslateService } from '@ngx-translate/core';

@Component({
    moduleId: module.id,
    selector: 'login',
    templateUrl: 'login.component.html'
})
export class LoginComponent extends BaseComponent implements OnInit {

    usuario: string;
    contrasenia: string;
    images: any[];

    constructor(
        private seguridadService: SeguridadService,
        private router: Router) {
        super();

    }

    ngOnInit() {
        sessionStorage.clear();
        localStorage.clear();
        this.images = [];
        this.images.push({ source: 'assets/layout/images/login/login1.jpg' });
        this.images.push({ source: 'assets/layout/images/login/login2.jpg' });
        this.images.push({ source: 'assets/layout/images/login/login3.jpg' });
        this.images.push({ source: 'assets/layout/images/login/login4.jpg' });
    }

    login(block?: any): void {

        block.blocked = true;

        this.seguridadService.validarUsuarioClave(this.usuario, this.contrasenia).then(obj => {

            console.log(obj);

            if (obj.status == 1) {
                sessionStorage.setItem('ua', JSON.stringify(obj));
                block.blocked = false;
                this.router.navigate(['mavitours/bienvenida']);
            }
            else {
                block.blocked = false;
                this.mostrarMensajes(this.getMensajesError(obj.messages));
            }

        });
    }
    myMethod(event?: any, block?: any) {
        if (event.keyCode == 13) {
            this.login(block);
        }
    }
}


