import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { ProveedorSaludService } from './proveedorsalud.service';
import { REGEXP_CORREO, REGEXP_TELEFONO, REGEXP_NOT_EMPTY } from './../constantes';
import { SelectItem } from 'primeng/primeng';
import { SeguridadService } from './../../home/seguridad/seguridad.service';
import { ProveedorSalud } from './proveedorsalud';
import { OnInit, Component } from '@angular/core';
import { BaseComponent } from './../../comun/basecomponent';

@Component({
    selector: 'proveedor-salud-mantenimiento',
    templateUrl: './proveedorsalud-mantenimiento.component.html'
})
export class ProveedorSaludMantenimientoComponent extends BaseComponent implements OnInit {

    accionNombre: string = "";
    verModal: boolean = false;
    proveedorSalud: ProveedorSalud = new ProveedorSalud();
    lstEstado: SelectItem[] = [];

    proveedorForm: FormGroup;

    constructor(private proveedorSaludService: ProveedorSaludService, private fb: FormBuilder) {
        super();
    }

    ngOnInit() {
        this.iniciarFormValidator();
    }

    iniciarFormValidator() {
        this.proveedorForm = this.fb.group({
            'id': new FormControl(null),
            'descripcion': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_NOT_EMPTY.source)]),
            'celular': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_TELEFONO.source)]),
            'telefono': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_TELEFONO.source)]),
            'estado': new FormControl(null, [Validators.required]),
            'correo': new FormControl(null, [Validators.required, Validators.pattern(REGEXP_CORREO.source)]),
        });
    }

    iniciarComponente() {
        this.accion = this.ACCIONES.NUEVO;
        this.accionNombre = this.getAccionNombre();
        this.verModal = true;
    }

    nuevo() {
        this.resetAllFormFields(this.proveedorForm);
        this.msgs = [];
        this.accion = this.ACCIONES.NUEVO;
        this.accionNombre = this.getAccionNombre();
        this.proveedorSalud = new ProveedorSalud();
        this.proveedorSalud.estado = 'A';

        this.lstEstado = [];
        this.lstEstado.push({ label: " -- Seleccionar -- ", value: null });
        this.lstEstado.push({ label: 'Activo', value: 'A' });
        this.lstEstado.push({ label: 'Inactivo', value: 'I' });
        this.verModal = true;
    }

    salir() {
        this.verModal = false;
    }

    guardar() {

        this.bloquearPagina();

        this.validateAllFormFields(this.proveedorForm);


        if (!this.proveedorForm.valid) {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: 'Error', detail: 'Ingresar correctamente los datos' });
            this.desbloquearPagina();
            return;
        }

        if (this.accion == this.ACCIONES.NUEVO) {
            this.proveedorSaludService.ingresar(this.proveedorSalud).then(
                res => {
                    this.enviarMensaje.emit(this.getMensajeRegistrado(res));
                    this.enviarListar.emit();
                    this.desbloquearPagina();
                    this.salir();
                }
            ).catch(res => {
                this.mostrarMensaje(this.getMensajeError(res));
                this.desbloquearPagina();
            });
        }
        else {
            this.proveedorSaludService.actualizar(this.proveedorSalud).then(
                res => {
                    this.enviarMensaje.emit(this.getMensajeRegistrado(res));
                    this.enviarListar.emit();
                    this.desbloquearPagina();
                    this.salir();
                }
            ).catch(res => {
                this.mostrarMensaje(this.getMensajeError(res));
                this.desbloquearPagina();
            });
        }
    }



    editar(bean: ProveedorSalud) {
        this.resetAllFormFields(this.proveedorForm);
        this.msgs = [];
        this.accion = this.ACCIONES.EDITAR;
        this.accionNombre = this.getAccionNombre();
        this.proveedorSalud = new ProveedorSalud();
        this.verModal = true;
        this.bloquearPagina();

        this.lstEstado = [];
        this.lstEstado.push({ label: " -- Seleccionar -- ", value: null });
        this.lstEstado.push({ label: 'Activo', value: 'A' });
        this.lstEstado.push({ label: 'Inactivo', value: 'I' });

        this.proveedorSaludService.obtenerPorId(bean.id).then(
            re => {
                this.proveedorSalud = re;
                this.desbloquearPagina();
            }
        );

    }

}



