import { BaseComponent } from './../../../comun/basecomponent';
import { SeguridadService } from './../../../home/seguridad/seguridad.service';
import { Component } from '@angular/core';
import { Router } from '@angular/router';
@Component({
    selector: 'contrasenia-mantenimiento',
    templateUrl: 'contrasenia-mantenimiento.component.html'
})
export class ContraseniaMantenientoComponent extends BaseComponent {

    verModal: boolean = false;
    usuario: string = "";
    clave: string = "";
    clave1: string = "";
    clave2: string = "";

    constructor(private seguridadService: SeguridadService) {
        super();
    };

    iniciarComponente(usuario: string) {
        console.log(1);
        this.usuario = usuario;
        this.clave = "";
        this.clave1 = "";
        this.clave2 = "";
        this.verModal = true;
    }

    guardar() {

        this.bloquearPagina();

        this.msgs = [];

        if (this.estaVacio(this.clave) || this.estaVacio(this.clave1) || this.estaVacio(this.clave2)) {
            this.msgs.push({ severity: 'error', summary: 'Error', detail: 'Ingresar todos los datos' });
            this.desbloquearPagina();
            return;
        }

        if (this.clave1 != this.clave2) {
            this.msgs.push({ severity: 'error', summary: 'Error', detail: 'Las claves no coinciden' });
            this.desbloquearPagina();
            return;
        }

        if (!this.validarClave()) {
            this.msgs.push({ severity: 'error', summary: 'Error', detail: 'Las nueva clave debe contener una letra mayscula, una minscula y un nmero' });
            this.desbloquearPagina();
            return;
        }

        this.seguridadService.cambiarClave(this.usuario, this.clave, this.clave1.trim(), this.clave2.trim()).then(
            res => {
                if (res.status == 200) {
                    this.desbloquearPagina();
                    this.msgs.push({ severity: 'info', summary: 'Informacin', detail: 'Se han guardado los cambios' });
                    this.salir();
                }
                else {
                    this.desbloquearPagina();
                    this.msgs.push({ severity: 'error', summary: 'Error', detail: res.statusText });
                }

            }
        );

    }

    validarClave(): boolean {
        return REGEXP_CLAVE.test(this.clave1.trim());
    }
    salir() {
        this.verModal = false;
    }

}

export const REGEXP_CLAVE = new RegExp('^(?=\\w*\\d)(?=\\w*[A-Z])(?=\\w*[a-z])\\S{1,50}$');



