import { DtoVehiculoVencimiento } from './dto-vehiculo-vencimiento';
import { Traslado } from './../traslado/traslado';
import { Vehiculo } from './vehiculo';
import { ParametroPaginacion } from './../../home/parametropaginacion';
import { Http, Headers, URLSearchParams } from '@angular/http';
import { Injectable } from '@angular/core';
import { REST_URL } from '../constantes';

@Injectable()
export class VehiculoService {

    private url = REST_URL + '/api/vehiculos/';
    private headers = new Headers({ 'Content-Type': 'application/json' });

    constructor(private http: Http) { }

    listarConPaginacion(filtro: Vehiculo, paginacion: ParametroPaginacion): Promise<ParametroPaginacion> {

        var params = new URLSearchParams();
        params.set("paginacion", JSON.stringify(paginacion));
        params.set("filtro", JSON.stringify(filtro));

        return this.http.get(`${this.url}listarconpaginacion`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.paginacion as ParametroPaginacion;
            }
            );
    }
    listarDisponibles(filtro: Vehiculo, traslado: Traslado): Promise<Vehiculo[]> {
        var params = new URLSearchParams();
        console.log(traslado);
        params.set("traslado", JSON.stringify(traslado));
        params.set("filtro", JSON.stringify(filtro));

        return this.http.get(`${this.url}listardisponibles`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data as Vehiculo[];
            }
            );
    }

    ingresar(vehiculo: Vehiculo): Promise<any> {

        return this.http.post(`${this.url}ingresar`, vehiculo, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo;
            }
        ).catch(
            res => {
                var respo = res.json();
                return respo;
            }
            );

    }

    listarPorVencer(tipo: string): Promise<DtoVehiculoVencimiento[]> {
        var params = new URLSearchParams();
        params.set("tipo", tipo);

        return this.http.get(`${this.url}listarporvencer`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data as DtoVehiculoVencimiento[];
            }
            );
    }

    obtenerPorId(id: number): Promise<Vehiculo> {
        var params = new URLSearchParams();
        params.set("id", JSON.stringify(id));

        return this.http.get(`${this.url}obtenerporid`, { search: params })
            .toPromise().then(response => {
                var p = response.json();
                return p.data[0] as Vehiculo;
            }
            );
    }

    actualizar(bean: Vehiculo): Promise<any> {
        return this.http.post(`${this.url}actualizar`, bean, { headers: this.headers }).toPromise().then(
            res => {
                var respo = res.json();
                return respo;
            }
        ).catch(
            res => {
                var respo = res.json();
                return respo;
            }
            );
    }
}



